import { ListItemButton, ListItem } from '@mui/material';
import React, { ReactNode } from 'react';

export const ListItemContainer = ({children, onClick}:{children: ReactNode, onClick?: () => void}) => onClick ? <ListItemButton sx={{p:0, minHeight: '4rem'}} onClick={onClick}>{children}</ListItemButton> : <ListItem component='div' sx={{p:0, minHeight: '4rem'}}>{children}</ListItem>;
