import { SalesOrderProduct } from '../containers/sales/types';
import {api} from './api';

export const salesOrdersApi = api.injectEndpoints({
	endpoints: (builder) => ({
		salesOrderListPublic: builder.mutation({
			query: (args: void) => ({
				url: 'salesOrders/list',
				method: 'GET',
			}),
		}),
		salesOrderDetailsLoad: builder.mutation({
			query: (orderId: string) => ({
				url: `salesOrders/${orderId}`,
				method: 'GET',
			}),
		}),
		salesOrderAddExistingProduct: builder.mutation({
			query: (data: Partial<SalesOrderProduct>) => ({
				url: 'salesOrderProducts/addExistingProduct',
				body: data,
				method: 'POST',
			}),
		}),
		salesOrderAddNewProduct: builder.mutation({
			query: (data: Partial<SalesOrderProduct>) => ({
				url: 'salesOrderProducts/addNewProduct',
				body: data,
				method: 'POST',
			}),
		}),
		salesOrderProductDelete: builder.mutation({
			query: (orderProductId: number) => ({
				url: `salesOrderProducts/${orderProductId}`,
				method: 'DELETE',
			}),
		}),
		salesOrderSubmitOffer: builder.mutation({
			query: (props: { orderId: string, data: object }) => ({
				url: `salesOrders/${props.orderId}/submitOffer`,
				body: props.data,
				method: 'POST',
			}),
		}),
		salesOrderCancelOffer: builder.mutation({
			query: (props: { orderId: string, data: object }) => ({
				url: `salesOrders/${props.orderId}/cancelOffer`,
				body: props.data,
				method: 'POST',
			}),
		}),
		salesOrderRejectOrder: builder.mutation({
			query: (props: { orderId: string, data: object }) => ({
				url: `salesOrders/${props.orderId}/reject`,
				body: props.data,
				method: 'POST',
			}),
		}),
		salesOrderSetStatusShipped: builder.mutation({
			query: (props: { orderId: string }) => ({
				url: `salesOrders/${props.orderId}/setStatusShipped`,
				method: 'POST',
			}),
		}),
	}),
});

export const {
	useSalesOrderAddExistingProductMutation,
	useSalesOrderAddNewProductMutation,
	useSalesOrderListPublicMutation,
	useSalesOrderDetailsLoadMutation,
	useSalesOrderProductDeleteMutation,
	useSalesOrderSubmitOfferMutation,
	useSalesOrderCancelOfferMutation,
	useSalesOrderRejectOrderMutation,
	useSalesOrderSetStatusShippedMutation
} = salesOrdersApi;
