import React, { useEffect } from 'react';
import PageWrapper from '../../components/PageWrapper';
import { useAuthEffects } from '../../components/Auth';
import { useTheme, Box, Grid2, useMediaQuery, Button, Typography, ImageList } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import { useNavigate, useParams } from 'react-router-dom';
import { useAppSelector } from '../../hooks';
import { ResourceProductsContainer } from '../plans/components/ResourceProductsContainer';
import { selectQuickOrderInfo, selectQuickOrderProducts, selectQuickOrderResources } from './quickOrderSlice';
import { useQuickOrderInfoMutation, useQuickOrderAddExistingProductMutation, useQuickOrderAddNewProductMutation, useQuickOrderDeleteProductMutation, useQuickOrderRemoveMutation } from '../../services/quickOrders';
import { useProductListLoadMutation } from '../../services/products';
import SalesOrderAddProductDialog from '../../components/SalesOrderAddProductDialog';
import { ResourceListItem, OrderProduct } from '../../types';
import { PlanListItemElV2 } from '../../components/PlanList';
import { calculateOrderTotals } from '../../common/util';

interface SalesOrderResourceListProps {
    resources: ResourceListItem[];
    products: OrderProduct[];
    onAddProductClick?: (resource: ResourceListItem) => void;
    onProductDeleteClick?: (orderProductId: number) => void;
    readOnly?: boolean;
}

export const QuickOrderResourceList = ({ resources, products, onAddProductClick, onProductDeleteClick, readOnly }: SalesOrderResourceListProps ) => {
    return <Grid2 container spacing={1} mt={1}>
        {resources.map((resource, i) => {
            const resourceProducts = products.filter(({resourceId}) => resourceId === resource.id);
            return <Grid2 size={12} key={i}>
                <ResourceProductsContainer
                    resource={resource}	
                    products={resourceProducts}
                    onAddProductClick={onAddProductClick}
                    onProductDeleteClick={onProductDeleteClick}
                    readOnly={readOnly}
                    showProductSku
                />
            </Grid2>;
        })}
    </Grid2>;
};

const QuickOrderDetailView = () => {
    useAuthEffects();

    const {id} = useParams();
    const theme = useTheme();
    const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));
    const [quickOrderInfoLoadMutation] = useQuickOrderInfoMutation();
    const [quickOrderAddExistingProductMutation, {isSuccess: isAddExistingProductSuccess}] = useQuickOrderAddExistingProductMutation();
    const [quickOrderAddNewProductMutation, {isSuccess: isAddNewProductSuccess}] = useQuickOrderAddNewProductMutation();
    const [quickOrderDeleteProductMutation, {isSuccess: isDeleteSuccess}] = useQuickOrderDeleteProductMutation();
    const [quickOrderRemoveMutation] = useQuickOrderRemoveMutation();
    const quickOrder = useAppSelector(selectQuickOrderInfo);
    const products = useAppSelector(selectQuickOrderProducts);
    const resources = useAppSelector(selectQuickOrderResources);
    const navigate = useNavigate();
    const [isAddProductDialogOpen, setIsAddProductDialogOpen] = React.useState(false);
    const [selectedResource, setSelectedResource] = React.useState<ResourceListItem | null>(null);
    const [productListLoadMutation] = useProductListLoadMutation();

    useEffect(() => {
        if (id) {
            quickOrderInfoLoadMutation(id);
        }
    }, [id]);

    useEffect(() => {
        if (isAddExistingProductSuccess || isAddNewProductSuccess) {
            setSelectedResource(null);
        }
    }, [isAddExistingProductSuccess, isAddNewProductSuccess]);

    const handleResourceListAddBtnClick = (resource: ResourceListItem) => {
        setSelectedResource(resource);
        setIsAddProductDialogOpen(true);
        productListLoadMutation();
    };

    const handleResourceListDeleteBtnClick = (orderProductId: number) => {
        if (id) {
            quickOrderDeleteProductMutation(orderProductId.toString());
        }
    };

    const handleAddProductSubmit = (payload: any, isExistingProduct: boolean) => {
        if (selectedResource && id) {
            if (isExistingProduct) {
                quickOrderAddExistingProductMutation({...payload, quickOrderId: Number(id)});
            } else {
                quickOrderAddNewProductMutation({...payload, quickOrderId: Number(id)});
            }
        }
        setIsAddProductDialogOpen(false);
    };

    const handleRemoveQuickOrderClick = () => {
        if (id) {
            quickOrderRemoveMutation(id);
            navigate('/quick-orders');
        }
    };

    const {subTotal, tax, shipping, total} = calculateOrderTotals(products.map((product) => ({pricePerUnit: product.pricePerUnit, quantity: Number(product.quantity)})));

    // const isStatusBeforePaidButHasProducts = quickOrder?.status === OrderStatus.Created && products.length > 0;
    // const isStatusPaidOrAfter = quickOrder && [OrderStatus.Paid, OrderStatus.Shipped].includes(quickOrder.status) ? true : false;

    return <PageWrapper>
        <Box sx={{mt: 4, mb: 2}}>
            {quickOrder && <Typography variant='body2'>
                Quick order
            </Typography>}
        </Box>
        <QuickOrderResourceList 
            resources={resources}
            products={products as unknown as OrderProduct[]}
            onAddProductClick={handleResourceListAddBtnClick}
            onProductDeleteClick={handleResourceListDeleteBtnClick}
        />
        <Grid2 container my={1} justifyContent='right'>
            <Grid2 size={isSmUp ? 'auto' : 12}>
                <Button size='large' startIcon={<CloseIcon />} color='error' onClick={handleRemoveQuickOrderClick}>Remove Quick Order</Button>
            </Grid2>
        </Grid2>
        <Grid2 container spacing={2} mt={4}>
            <Grid2 size={12}>
                <Typography variant='body2'>
                    Plan
                </Typography>
                {quickOrder?.plan && <Box mt={1}>
                    <PlanListItemElV2 item={quickOrder.plan} />
                </Box>}
            </Grid2>
        </Grid2>
        <SalesOrderAddProductDialog
            resource={selectedResource}
            onSubmit={handleAddProductSubmit}
            onClose={() => setSelectedResource(null)}
        />
    </PageWrapper>;
};

export default QuickOrderDetailView;
