import {createSelector, createSlice, PayloadAction} from '@reduxjs/toolkit';
import {RootState} from '../../store';
import { quickOrdersApi } from '../../services/quickOrders';
import { QuickOrderInfo, QuickOrderListItem } from './types';
import { QuickOrderProduct, ResourceListItem } from '../../types';

interface QuickOrderState {
	quickOrdersListItems: QuickOrderListItem[];
	quickOrderInfo: QuickOrderInfo | null;
	resources: ResourceListItem[];
	products: QuickOrderProduct[];
}

const initialState: QuickOrderState = {
	quickOrdersListItems: [],
	quickOrderInfo: null,
	resources: [],
	products: [],
};

export const quickOrderSlice = createSlice({
	name: 'quickOrders',
	initialState,
	reducers: {
		quickOrdersListItemsSetState(state: QuickOrderState, action) {
			state.quickOrdersListItems = action.payload ?? [];
		},
		quickOrderInfoSetState(state: QuickOrderState, action) {
			const {resources, products, ...quickOrderInfo} = action.payload;
			state.quickOrderInfo = quickOrderInfo;
			state.resources = resources;
			state.products = products;
		},
		quickOrderProductAdd(state: QuickOrderState, action: PayloadAction<QuickOrderProduct>) {
			state.products.push(action.payload);
		},
		quickOrderProductRemove(state: QuickOrderState, action: PayloadAction<number>) {
			state.products = state.products.filter(product => product.id !== action.payload);
		}
	},
	extraReducers: (builder) => {
		builder
			.addMatcher(quickOrdersApi.endpoints.quickOrderInfo.matchFulfilled, (state: QuickOrderState, action: PayloadAction<QuickOrderInfo>) => {
				quickOrderSlice.caseReducers.quickOrderInfoSetState(state, quickOrderSlice.actions.quickOrderInfoSetState(action.payload));
			})
			.addMatcher(quickOrdersApi.endpoints.quickOrdersList.matchFulfilled, (state: QuickOrderState, action: PayloadAction<QuickOrderListItem[]>) => {
				quickOrderSlice.caseReducers.quickOrdersListItemsSetState(state, quickOrderSlice.actions.quickOrdersListItemsSetState(action.payload));
			})
			.addMatcher(quickOrdersApi.endpoints.quickOrderAddExistingProduct.matchFulfilled, (state: QuickOrderState, action: PayloadAction<QuickOrderProduct>) => {
				quickOrderSlice.caseReducers.quickOrderProductAdd(state, quickOrderSlice.actions.quickOrderProductAdd(action.payload));
			})
			.addMatcher(quickOrdersApi.endpoints.quickOrderAddNewProduct.matchFulfilled, (state: QuickOrderState, action: PayloadAction<QuickOrderProduct>) => {
				quickOrderSlice.caseReducers.quickOrderProductAdd(state, quickOrderSlice.actions.quickOrderProductAdd(action.payload));
			})
			.addMatcher(quickOrdersApi.endpoints.quickOrderDeleteProduct.matchFulfilled, (state: QuickOrderState, action: PayloadAction<Pick<QuickOrderProduct, 'id'>>) => {
				quickOrderSlice.caseReducers.quickOrderProductRemove(state, quickOrderSlice.actions.quickOrderProductRemove(action.payload.id));
			});

	},
});

export const {
	quickOrdersListItemsSetState,
} = quickOrderSlice.actions;

export const selectQuickOrderState = (state: RootState) => state.quickOrders;

export const selectQuickOrdersListItems = createSelector([selectQuickOrderState], (state) => state.quickOrdersListItems);

export const selectQuickOrderInfo = createSelector([selectQuickOrderState], (state) => state.quickOrderInfo);

export const selectQuickOrderProducts = createSelector([selectQuickOrderState], (state) => state.products);

export const selectQuickOrderResources = createSelector([selectQuickOrderState], (state) => state.resources);

export default quickOrderSlice.reducer;
