import { useMediaQuery, useTheme, Box, Card, CardContent, Typography, List, ListItem, ListItemText, Button, Collapse, IconButton, ListItemAvatar, Avatar, Divider } from '@mui/material';
import ShoppingCartCheckoutIcon from '@mui/icons-material/ShoppingCartCheckout';
import EditIcon from '@mui/icons-material/Edit';
import AddIcon from '@mui/icons-material/Add';
import React from 'react';
import { QuickOrder, ResourceListItem } from '../../../types';
import { OrderSubmitData } from '../../../services/orders';
import { useState } from 'react';
import { PlanInfoQuickOrder } from '../types';
import useAuth from '../../../components/Auth';

interface QuickOrderCardProps {
	list: PlanInfoQuickOrder;
	resources: ResourceListItem[];
	onQuantityChange?: (productId: number, newQuantity: number) => void;
	onOrderSubmit?: (data: OrderSubmitData) => void;
	onEdit?: () => void;
}

interface CreateQuickOrderCardProps {
	onCreate: () => void;
}

export const CreateQuickOrderCard = ({ onCreate }: CreateQuickOrderCardProps) => {
	const isSmUp = useMediaQuery(useTheme().breakpoints.up('sm'));

	return (
		<Card sx={{ 
			minWidth: isSmUp ? '350px' : '300px',
			maxWidth: '350px',
			flex: isSmUp ? '1 0 350px' : '1 0 300px',
			display: 'flex',
			flexDirection: 'column',
			justifyContent: 'center',
			alignItems: 'center',
			cursor: 'pointer',
			'&:hover': {
				bgcolor: 'action.hover',
			},
		}} onClick={onCreate}>
			<Box sx={{ 
				display: 'flex', 
				flexDirection: 'column',
				alignItems: 'center',
				gap: 2,
				p: 4,
			}}>
				<AddIcon sx={{ fontSize: 48, color: 'primary.main' }} />
				<Typography variant="h6" color="primary">
					Create Quick Order
				</Typography>
				<Typography variant="body2" color="text.secondary" align="center">
					Prepare a shopping list for this project to allow customers easily place orders to your store
				</Typography>
			</Box>
		</Card>
	);
};

export const QuickOrderCard = ({ list, resources, onQuantityChange, onOrderSubmit, onEdit }: QuickOrderCardProps) => {
	const isSmUp = useMediaQuery(useTheme().breakpoints.up('sm'));
	const { userProfile } = useAuth();
	const isAuthor = userProfile?.companyId === list.vendor.id;
	const total = list.products.reduce((sum, item) => sum + (Number(item.pricePerUnit) * Number(item.quantity)), 0);
	const [expandedItem, setExpandedItem] = useState<number | null>(null);

	// Create a lookup map for resources by their ID
	const resourcesById = new Map<number, ResourceListItem>(
		resources.map((resource: ResourceListItem) => [resource.id, resource])
	);

	// Create a lookup map for products by their ID
	const productsById = new Map<number, typeof list.products[0]>(
		list.products.map(product => [product.id, product])
	);

	// Separate products into tools and non-tools based on their corresponding resources
	const toolProducts = list.products.filter(product => {
		const resource = resourcesById.get(product.resourceId);
		return !resource || resource?.isTool;
	});
	const nonToolProducts = list.products.filter(product => {
		const resource = resourcesById.get(product.resourceId);
		return resource && !resource?.isTool;
	});

	const handleOrderSubmit = () => {
		onOrderSubmit?.({
			quickOrderId: list.id,
			items: list.products.map((product) => ({
				id: product.id, quantity: product.quantity
			})).filter(item => item.quantity > 0)
		});
	};

	const renderProductList = (products: typeof list.products) => (
		<List>
			{products.map((item, i) => (
				<ListItem 
					key={item.id} 
					divider={i < products.length - 1}
					sx={{ flexDirection: 'column', alignItems: 'stretch' }}
				>
					<Box sx={{ 
						display: 'flex', 
						justifyContent: 'space-between', 
						alignItems: 'center',
						width: '100%'
					}}>
						<Box sx={{ display: 'flex', alignItems: 'center', flex: 1 }}>
							<ListItemAvatar>
								<Avatar
									src={item.imgUrl || '/placeholder-product.png'}
									alt={item.name}
									variant="rounded"
									sx={{ width: 40, height: 40 }}
								/>
							</ListItemAvatar>
							<ListItemText 
								primary={item.name}
								secondary={`$${item.pricePerUnit} per ${item.uom}`}
							/>
						</Box>
						<Box sx={{ textAlign: 'right' }}>
							{item.quantity > 0 && (
								<Typography 
									color="primary"
									sx={{ mb: 0.5 }}
								>
									${(Number(item.pricePerUnit) * Number(item.quantity)).toFixed(2)}
								</Typography>
							)}
							<Button
								size="small"
								onClick={() => {
									if (item.quantity === 0) {
										onQuantityChange?.(item.id, 1);
									} else {
										setExpandedItem(expandedItem === i ? null : i);
									}
								}}
								sx={{ 
									minWidth: 'auto',
									p: 0.5,
									typography: 'body2'
								}}
							>
								{item.quantity === 0 ? 'ADD' : `${item.quantity} ${item.uom}`}
							</Button>
						</Box>
					</Box>
					<Collapse in={expandedItem === i}>
						<Box sx={{ 
							display: 'flex', 
							alignItems: 'center',
							gap: 1,
							mt: 1,
							mb: 1,
							justifyContent: 'center'
						}}>
							<Button 
								size="small"
								variant="outlined"
								onClick={() => {
									const newQuantity = Math.max(0, Number(item.quantity) - 1);
									onQuantityChange?.(item.id, newQuantity);
									if (newQuantity === 0) {
										setExpandedItem(null);
									}
								}}
							>
								-
							</Button>
							<Typography sx={{ minWidth: '60px', textAlign: 'center' }}>
								{item.quantity} {item.uom}
							</Typography>
							<Button 
								size="small"
								variant="outlined"
								onClick={() => onQuantityChange?.(item.id, Number(item.quantity) + 1)}
							>
								+
							</Button>
						</Box>
					</Collapse>
				</ListItem>
			))}
		</List>
	);

	return (
		<Card sx={{ 
			minWidth: isSmUp ? '350px' : '300px',
			maxWidth: '350px',
			flex: isSmUp ? '1 0 350px' : '1 0 300px',
		}}>
			<Box sx={{ position: 'relative' }}>
				<Box
					component="img"
					src={list.vendor.imgUrl}
					alt={list.title}
					sx={{
						width: '100%',
						height: 140,
						objectFit: 'cover',
					}}
				/>
				<Box
					sx={{
						position: 'absolute',
						bottom: 0,
						left: 0,
						right: 0,
						background: 'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0) 100%)',
						padding: 2,
						paddingTop: 4,
					}}
				>
					<Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
						<Typography variant="h6" color="white">
							{list.title}
						</Typography>
						{isAuthor && onEdit && (
							<IconButton 
								size="small" 
								onClick={onEdit}
								sx={{ color: 'white' }}
							>
								<EditIcon />
							</IconButton>
						)}
					</Box>
				</Box>
			</Box>
			<CardContent>
				{nonToolProducts.length > 0 && renderProductList(nonToolProducts)}
				{toolProducts.length > 0 && (
					<>
						<Divider sx={{ my: 2 }} />
						<Typography variant="subtitle2" color="text.secondary" sx={{ mb: 1 }}>
							Suggested items
						</Typography>
						{renderProductList(toolProducts)}
					</>
				)}
				<Box sx={{ 
					mt: 2, 
					pt: 2, 
					borderTop: 1, 
					borderColor: 'divider',
				}}>
					<Box sx={{
						display: 'flex',
						justifyContent: 'space-between',
						alignItems: 'center',
						mb: 2
					}}>
						<Typography variant="subtitle1">Total</Typography>
						<Typography variant="h6" color="primary">
							${total.toFixed(2)}
						</Typography>
					</Box>
					<Button 
						variant="contained" 
						fullWidth
						startIcon={<ShoppingCartCheckoutIcon />}
						onClick={handleOrderSubmit}
					>
						To checkout
					</Button>
				</Box>
			</CardContent>
		</Card>
	);
};
