import { Box, Button, DialogContent, DialogTitle, InputAdornment, IconButton, TextField, Avatar, debounce, Grid2, useTheme } from '@mui/material';
import { Dialog } from '@mui/material';
import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { OrderProduct, ResourceListItem } from '../types';
import { Close as CloseIcon, Category as CategoryIcon, HourglassBottom as HourglassBottomIcon, Verified as VerifiedIcon } from '@mui/icons-material';
import { useProductsFindMutation } from '../services/products';

interface Props {
    resource: ResourceListItem | null;
    onSubmit: (payload: Partial<OrderProduct>, isExistingProduct: boolean) => void;
    onClose: () => void;
}

const SalesOrderAddProductDialog = ({ resource, onSubmit, onClose }: Props) => {
	const [formData, setFormData] = useState<Partial<OrderProduct>>({});
	const [productsFindMutation] = useProductsFindMutation(); 
	const [skuMatch, skuMatchSetState] = useState<OrderProduct | null | false>(null);
	const theme = useTheme();

	useEffect(() => {
		if (resource) {
			setFormData({
				resourceId: resource.id,
				quantity: resource.quantity,
				uom: resource.uom,
			});
		}
	}, [resource]);

	const handleFormFieldChange = (e: React.ChangeEvent<any>) => {
		setFormData({
			...formData,
			[e.target.name]: e.target.value
		});
	};

	const onAccept = useCallback((sku: string) => {
		productsFindMutation({
			where: {
				sku: sku
			}
		}).unwrap().then((results) => {
			const [match] = results;
			skuMatchSetState(match ?? false);
		});
	}, []);

	useEffect(() => {
		if (skuMatch) {
			setFormData({
				...formData,
				uom: skuMatch.uom,
				name: skuMatch.name,
				pricePerUnit: skuMatch.pricePerUnit,
				imgUrl: skuMatch.imgUrl,
			});
		}
		if (skuMatch === false) {
			setFormData({
				...formData,
				uom: resource ? resource.uom : '',
				name: '',
				pricePerUnit: '',
				imgUrl: '',
			});
		}
	}, [skuMatch]);

	const handleSKUChangeDebounced = React.useMemo(
		() => debounce(onAccept, 500),
		[onAccept],
	);

	const handleSKUChange = (e: React.ChangeEvent<any>) => {
		handleFormFieldChange(e);
		handleSKUChangeDebounced(e.target.value);
	};

	const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
		e.preventDefault();
		onSubmit(formData, Boolean(skuMatch));
	};

	const isSKUEmpty = useMemo(() => !formData.sku, [formData]);

	return <Dialog open={Boolean(resource)} fullWidth onClose={onClose}>
		<DialogTitle>
			{formData?.name}
		</DialogTitle>
		<IconButton onClick={onClose} sx={(theme) => ({
			position: 'absolute',
			right: 8,
			top: 8,
			color: theme.palette.grey[500],
		})}>
			<CloseIcon />
		</IconButton>
		<DialogContent>
			<form onSubmit={handleSubmit}>
				<Grid2 container spacing={2}>
					<Grid2>
						<TextField fullWidth required autoComplete='off' label='SKU' name='sku' value={formData.sku ?? ''} onChange={handleSKUChange} />
					</Grid2>
					<Grid2 display='flex' alignItems='center'>
						{(skuMatch === null && !isSKUEmpty) && <Box display='flex' alignItems='center'><HourglassBottomIcon/></Box>}
						{(skuMatch === false && !isSKUEmpty) && <Box display='flex' alignItems='center'><i>Adding new product</i></Box>}
						{(!isSKUEmpty && skuMatch) && <Box display='flex' alignItems='center'><VerifiedIcon color='info' sx={{mr:1}} /></Box>}
					</Grid2>
					<Grid2 size={12}>
						<TextField fullWidth sx={{bgcolor: skuMatch ? theme.palette.action.disabledBackground : undefined}} disabled={Boolean(skuMatch)} required autoComplete='off' label='Name' name='name' value={formData.name ?? ''} onChange={handleFormFieldChange} />
					</Grid2>
					<Grid2 size='grow'>
						<TextField fullWidth required type='number' autoComplete='off' label='Quantity' name='quantity' value={formData.quantity ?? ''} onChange={handleFormFieldChange} />
					</Grid2>
					<Grid2 size='grow'>
						<TextField sx={{bgcolor: skuMatch ? theme.palette.action.disabledBackground : undefined}} disabled={Boolean(skuMatch)} required autoComplete='off' label='Unit of measure' name='uom' value={formData.uom ?? ''} onChange={handleFormFieldChange} />
					</Grid2>
					<Grid2 size='grow'>
						<TextField required type='number' autoComplete='off' label='Price per unit' name='pricePerUnit' value={formData.pricePerUnit ?? ''} onChange={handleFormFieldChange} InputProps={{
							endAdornment: <InputAdornment position="end">€</InputAdornment>,
						}}/>
					</Grid2>
					<Grid2 size={12}>
						<TextField sx={{mb:2, bgcolor: skuMatch ? theme.palette.action.disabledBackground : undefined}} disabled={Boolean(skuMatch)} autoComplete='off' label='Image URL' name='imgUrl' value={formData.imgUrl ?? ''} onChange={handleFormFieldChange} />
						<Avatar sx={{width: 96, height: 96}} sizes='large' variant='square' src={formData.imgUrl}>
							<CategoryIcon fontSize='large' />
						</Avatar>
					</Grid2>
					<Grid2 size={12}>
						<Button type='submit' variant='contained'>Add</Button>
					</Grid2>
				</Grid2>
			</form>
		</DialogContent>
	</Dialog>;   
};

export default SalesOrderAddProductDialog;
