import React, { useEffect, useState } from 'react';
import PageWrapper from '../../components/PageWrapper';
import { Alert, Avatar, Box, Button, Dialog, DialogActions, DialogContent, DialogTitle, Drawer, List, ListItemAvatar, ListItemButton, ListItemText, Snackbar, TextField, Typography } from '@mui/material';
import FolderIcon from '@mui/icons-material/Folder';
import { useNavigate } from 'react-router-dom';
import { usePlanAddFromYTMutation, usePlansListMutation } from '../../services/plans';
import { useAppSelector } from '../../hooks';
import { selectUserProfile } from '../accountSlice';
import { useAuthEffects } from '../../components/Auth';
import { selectPlansOwned } from './planSlice';
import { SimpleFormField, SimpleFormPropertyType, SimpleFormTemplateProperty, useSimpleForm } from '../../components/SimpleForm';
import CheckIcon from '@mui/icons-material/Check';
import UnpublishedIcon from '@mui/icons-material/Unpublished';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { error } from 'console';
import { FetchBaseQueryError } from '@reduxjs/toolkit/query';

const FORM_TEMPLATE: SimpleFormTemplateProperty<{ videoId: string }>[] = [{
	label: 'Video id',
	name: 'videoId',
	type: SimpleFormPropertyType.String,
	required: true,
}];

export default function PlansView() {
	useAuthEffects();

	const plans = useAppSelector(selectPlansOwned);
	const userProfile = useAppSelector(selectUserProfile);
	const [plansListMutation] = usePlansListMutation();
	const [addDialogIsOpen, setAddDialogIsOpen] = useState<boolean>(false);
	const [planAddFromYTMutation, {error: planAddFromYTMutationError, isSuccess: planAddFromYTMutationIsSuccess}] = usePlanAddFromYTMutation();
	const {formData, handleFormFieldChange} = useSimpleForm<{videoId: string}>(FORM_TEMPLATE);

	const addFromYTHandleFormSubmit = (e: React.FormEvent<HTMLFormElement>) => {
		e.preventDefault();
		planAddFromYTMutation({ videoId: formData.videoId!.trim() });
	};

	const [isErrorState, setIsErrorState] = useState(false);
	const [isSuccessState, setIsSuccessState] = useState(false);

	useEffect(() => {
		if (planAddFromYTMutationError) {
			setIsErrorState(true);
		}
	}, [planAddFromYTMutationError]);

	useEffect(() => {
		if (planAddFromYTMutationIsSuccess) {
			setIsSuccessState(true);
			setAddDialogIsOpen(false);
		}
	}, [planAddFromYTMutationIsSuccess]);

	useEffect(() => {
		if (userProfile) {
			plansListMutation();
		}
	}, [userProfile]);
	
	const handleCloseSnackbar = () => {
		setIsSuccessState(false);
		setIsErrorState(false);
	};

	const navigate = useNavigate();
	return <PageWrapper disableFooter={!plans.length} noScroll={!plans.length}>
		{!plans.length ? <Box flex={1} display='flex' alignItems='center' justifyContent='center'>
			<Typography>No plans yet !</Typography>
		</Box> :
			<List sx={{pb:5}}>
				{plans.map((plan, i) => <ListItemButton key={i} onClick={() => navigate(`/plans/${plan.id}/edit`)} sx={{cursor:'pointer'}}>
					<ListItemAvatar>
						<Avatar src={plan.imgUrl} variant='square' sx={{ width: 96, height: 96, mr: 2, borderRadius: 2 }}>
							<FolderIcon />
						</Avatar>
					</ListItemAvatar>
					<ListItemText>{plan.title}</ListItemText>
					<Box>
						{plan.publishedAt ? <CheckCircleIcon color='success' /> : <UnpublishedIcon color='warning' /> }
					</Box>
				</ListItemButton>)}
			</List>}
		<Dialog open={addDialogIsOpen} onClose={() => setAddDialogIsOpen(false)}>
			<form onSubmit={addFromYTHandleFormSubmit}>
				<DialogTitle>Import from YouTube</DialogTitle>
				<DialogContent>
					<SimpleFormField property={FORM_TEMPLATE[0]} value={formData.videoId ?? ''} onChange={handleFormFieldChange} />
				</DialogContent>
				<DialogActions>
					<Button onClick={() => setAddDialogIsOpen(false)}>Cancel</Button>
					<Button disabled={isErrorState} type='submit' variant='contained'>Add</Button>
				</DialogActions>
			</form>
		</Dialog>
		<Drawer variant='permanent' anchor='bottom'>
			<Box p={2}>
				<Button onClick={() => setAddDialogIsOpen(!addDialogIsOpen)}>Import from YouTube</Button>
			</Box>
		</Drawer>
		<Snackbar
			open={isSuccessState}
			anchorOrigin={{ vertical: 'bottom', horizontal: 'center'}}
			autoHideDuration={5000}
			onClose={handleCloseSnackbar}
		>
			<Alert
				severity="success"
				sx={{ width: '100%' }}
			>
				Project imported!
			</Alert>
		</Snackbar>
		<Snackbar
			open={isErrorState}
			anchorOrigin={{ vertical: 'bottom', horizontal: 'center'}}
			autoHideDuration={5000}
			onClose={handleCloseSnackbar}
		>
			<Alert
				severity="error"
				sx={{ width: '100%' }}
			>
				Could not add video: {JSON.stringify((planAddFromYTMutationError as FetchBaseQueryError)?.data)}
			</Alert>
		</Snackbar>
	</PageWrapper>;
}
