import {api} from './api';
import {SWAP_TOKEN_URL_PATH} from '../constants';

export interface MyCompany {
	id: number;
	name: string;
	bio: string;
	imgUrl: string | null
	roleCustomer: boolean;
	roleCreator: boolean;
	roleVendor: boolean;
}

export interface MyAccount {
	id: string;
	companyId: string;
	email: string;
	firstName: string;
	lastName: string;
	name: string;
}

export interface MyAccountResponse extends MyAccount {
	company: MyCompany;
}

interface SwapTokenRequest {
	auth0Token: string;
}

interface SwapTokenResponse {
	token: string;
}

export const accountsApi = api.injectEndpoints({
	endpoints: (builder) => ({
		swapToken: builder.mutation<SwapTokenResponse, SwapTokenRequest>({
			query: ({ auth0Token }) => ({
				url: SWAP_TOKEN_URL_PATH,
				method: 'POST',
				body: {
					auth0Token,
				},
			}),
			extraOptions: { maxRetries: 1 }
		}),
		getMyAccount: builder.mutation<MyAccountResponse, void>({
			query: () => ({
				url: 'accounts/myAccount',
				method: 'GET',
			}),
			extraOptions: { maxRetries: 1 }
		}),
		updateMyCompany: builder.mutation<Partial<MyCompany>, Partial<MyCompany>>({
			query: (data) => ({
				url: 'company/myCompany',
				method: 'PUT',
				body: data,
			}),
			extraOptions: { maxRetries: 1 }
		}),
	}),
});

export const { useSwapTokenMutation, useGetMyAccountMutation, useUpdateMyCompanyMutation } = accountsApi;
