import React, { useEffect, useMemo, useState } from 'react';
import PageWrapper from '../../components/PageWrapper';
import { useAuthEffects } from '../../components/Auth';
import { useTheme, Box, Grid2, Tab, Tabs, useMediaQuery, Button, ImageList, Typography, Card, CardContent, Alert} from '@mui/material';
import ChatBox from '../../components/Chatbox';
import Stepper from '../orders/components/Stepper';
import CloseIcon from '@mui/icons-material/Close';
import { useNavigate, useParams } from 'react-router-dom';
import { useAppSelector } from '../../hooks';
import { ResourceProductsContainer } from '../plans/components/ResourceProductsContainer';
import { selectSalesOrderCustomer, selectSalesOrderDetails, selectSalesOrderMessages, selectSalesOrderPlan, selectSalesOrderProducts, selectSalesOrderResources } from './salesSlice';
import { useSalesOrderDetailsLoadMutation, useSalesOrderAddNewProductMutation, useSalesOrderAddExistingProductMutation, useSalesOrderProductDeleteMutation, useSalesOrderSubmitOfferMutation, useSalesOrderRejectOrderMutation, useSalesOrderCancelOfferMutation, useSalesOrderSetStatusShippedMutation } from '../../services/salesOrders';
import LocalOfferIcon from '@mui/icons-material/LocalOffer';
import { useProductListLoadMutation } from '../../services/products';
import SalesOrderAddProductDialog from '../../components/SalesOrderAddProductDialog';
import { OrderStatus, ResourceListItem } from '../../types';
import { PlanListItemElV2 } from '../../components/PlanList';
import AssignmentTurnedInIcon from '@mui/icons-material/AssignmentTurnedIn';
import { OrderPaymentTotals } from '../orders/components/OrderPaymentTotals';
import { calculateOrderTotals } from '../../common/util';
import { SalesOrderProduct } from './types';
function a11yProps(index: number) {
	return {
		id: `simple-tab-${index}`,
		'aria-controls': `simple-tabpanel-${index}`,
	};
}

interface TabPanelProps {
	children?: React.ReactNode;
	index: number;
	value: number;
}

function CustomTabPanel(props: TabPanelProps) {
	const { children, value, index, ...other } = props;
	return (
		<Box
			flex={1}
			// overflow='auto'
			role="tabpanel"
			hidden={value !== index}
			id={`simple-tabpanel-${index}`}
			aria-labelledby={`simple-tab-${index}`}
			{...other}
		>
			{children}
		</Box>
	);
}

interface SalesOrderResourceListProps {
    resources: ResourceListItem[];
	products: SalesOrderProduct[];
	onAddProductClick?: (resource: ResourceListItem) => void;
	onProductDeleteClick?: (productId: number) => void;
	readOnly?: boolean;
}

export const SalesOrderResourceList = ({ resources, products, onAddProductClick, onProductDeleteClick, readOnly }: SalesOrderResourceListProps ) => {
	return <Grid2 container spacing={1} mt={1}>
		{resources.map((resource, i) => {
			const resourceProducts = products.filter(({resourceId}) => resourceId === resource.id);
			return <Grid2 size={12} key={i}>
				<ResourceProductsContainer
					key={i}
					resource={resource}	
					products={resourceProducts}
					onAddProductClick={onAddProductClick}
					onProductDeleteClick={onProductDeleteClick}
					readOnly={readOnly}
					showProductSku
				/>
			</Grid2>;
		})}
	</Grid2>;
};

const SalesDetailView = () => {
	useAuthEffects();

	const {id} = useParams();
	const theme = useTheme();
	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));
	const navigate = useNavigate();
	const [salesOrderDetailsLoadMutation] = useSalesOrderDetailsLoadMutation();
	const [salesOrderAddExistingProduct] = useSalesOrderAddExistingProductMutation();
	const [salesOrderAddNewProduct] = useSalesOrderAddNewProductMutation();
	const [salesOrderProductDelete] = useSalesOrderProductDeleteMutation();
	const [productListLoadMutation] = useProductListLoadMutation();
	const [salesOrderSubmitOfferMutation] = useSalesOrderSubmitOfferMutation();
	const [salesOrderCancelOfferMutation] = useSalesOrderCancelOfferMutation();
	const [salesOrderRejectOrderMutation] = useSalesOrderRejectOrderMutation();
	const [salesOrderSetStatusShippedMutation] = useSalesOrderSetStatusShippedMutation();
	const [tabValue, setTabValue] = React.useState(0);
	const order = useAppSelector(selectSalesOrderDetails);
	const messages = useAppSelector(selectSalesOrderMessages);
	const resources = useAppSelector(selectSalesOrderResources);
	const products = useAppSelector(selectSalesOrderProducts);
	const plan = useAppSelector(selectSalesOrderPlan);
	const customer = useAppSelector(selectSalesOrderCustomer);

	const [addProductDialogSelectedResource, addProductDialogSelectedResourceSetState] = useState<ResourceListItem | null>(null);

	const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
		setTabValue(newValue);
	};

	const closeAddProductDialog = () => {
		addProductDialogSelectedResourceSetState(null);
	};

	const handleResourceListAddBtnClick = (resource: ResourceListItem) => {
		addProductDialogSelectedResourceSetState(resource);
	};

	const handleResourceListDeleteBtnClick = (orderProductId: number) => {
		salesOrderProductDelete(orderProductId);
	};

	const addProductHandleSubmit = (payload: Partial<SalesOrderProduct>, isExistingProduct: boolean) => {
		const mutation = isExistingProduct ? salesOrderAddExistingProduct : salesOrderAddNewProduct;
		mutation({
			orderId: Number(id),
			...payload
		}).unwrap().then(() => {
			closeAddProductDialog();
		}).catch((e) => {
			console.error(e);
		});
	};

	const handleSendOfferClick = () => {
		if (id) {
			salesOrderSubmitOfferMutation({ orderId: id, data: {}});
		}
	};

	const handleRejectOrderClick = () => {
		if (id) {
			salesOrderRejectOrderMutation({ orderId: id, data: {}});
		}
	};

	const handleCancelOfferClick = () => {
		if (id) {
			salesOrderCancelOfferMutation({ orderId: id, data: {}});
		}
	};

	const handleSetStatusShippedClick = () => {
		if (id) {
			salesOrderSetStatusShippedMutation({ orderId: id });
		}
	};

	useEffect(() => {
		if (id) {
			salesOrderDetailsLoadMutation(id);
			productListLoadMutation();
		}
	}, [id]);

	const {subTotal, tax, shipping, total} = calculateOrderTotals(products.map((product) => ({pricePerUnit: product.pricePerUnit, quantity: Number(product.quantity)})));
	const isStatusBeforePaidButHasProducts = order && Boolean(products.length) && ![OrderStatus.Paid, OrderStatus.Shipped].includes(order.status);
	const isStatusPaidOrAfter = order && [OrderStatus.Paid, OrderStatus.Shipped].includes(order.status) ? true : false;
	const hasAllResources = useMemo(() => !resources.find((resource) => !products.find((product) => product.resourceId === resource.id)), [resources, products]);

	return <PageWrapper noScroll={tabValue === 1} disableFooter={tabValue === 1}>
		<Box sx={{mt: 4, mb: 2}}>
			{order && <Stepper 
				orderStatus={order.status}
				orderDate={order.createdAt}
			/>}
		</Box>
		<Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
			<Tabs value={tabValue} onChange={handleTabChange} aria-label="basic tabs example">
				<Tab label="Items" {...a11yProps(0)} />
				<Tab label="Messages" {...a11yProps(1)} />
			</Tabs>
		</Box>
		<CustomTabPanel value={tabValue} index={0}>
			<SalesOrderResourceList 
				resources={resources}
				products={products}
				onAddProductClick={handleResourceListAddBtnClick}
				onProductDeleteClick={handleResourceListDeleteBtnClick}
				readOnly={order?.status !== OrderStatus.Created}
			/>
			{isStatusBeforePaidButHasProducts && 
				<OrderPaymentTotals 
					subTotal={subTotal}
					tax={false}
					shipping={false}
					total={false}
				/>
			}
			{isStatusPaidOrAfter && 
				<OrderPaymentTotals 
					subTotal={subTotal}
					tax={tax}
					shipping={shipping}
					total={total}
					isPaid={isStatusPaidOrAfter}
				/>
			}
			{order?.status === OrderStatus.Created && <Grid2 container my={1} justifyContent='right'>
				{!hasAllResources && <Grid2 size={isSmUp ? 'grow' : 12}>
					<Typography my={1}>Include a product for each requested item!</Typography>
				</Grid2>}
				<Grid2 size={isSmUp ? 'auto' : 12}>
					<Button size='large' startIcon={<LocalOfferIcon />} disabled={!hasAllResources} color='success' onClick={handleSendOfferClick}>Send offer</Button>
					<Button size='large' startIcon={<CloseIcon />} color='error' onClick={handleRejectOrderClick}>Reject order</Button>
				</Grid2>
			</Grid2>}
			{order?.status === OrderStatus.Prepared && <Grid2 container my={1}>
				<Grid2 size={isSmUp ? 'grow' : 12}>
					<Typography my={1}>Waiting for the customer to accept the offer!</Typography>
				</Grid2>
				<Grid2 size={isSmUp ? 'auto' : 12}>
					<Button startIcon={<CloseIcon />} color='error' onClick={handleCancelOfferClick}>Cancel offer</Button>
				</Grid2>
			</Grid2>}
			{order?.status === OrderStatus.Rejected && <Grid2 container my={1}>
				<Grid2>
					<Typography my={1}>You have rejected this order.</Typography>
				</Grid2>
			</Grid2>}
			{order?.status === OrderStatus.Paid && <Grid2 container my={1} justifyContent='space-between'>
				<Grid2>
					<Typography my={1}>
						This order awaits shipping!
					</Typography>
				</Grid2>
				<Grid2 size={isSmUp ? 'auto' : 12}>
					<Button size='large' startIcon={<AssignmentTurnedInIcon />} onClick={handleSetStatusShippedClick} variant='contained' color='success'>Mark as shipped</Button>
				</Grid2>
			</Grid2>}
			{order?.status === OrderStatus.Shipped && <Grid2 container my={1} justifyContent='space-between'>
				<Grid2>
					<Typography my={1}>
						This order has been shipped!
					</Typography>
				</Grid2>
				<Grid2 size={isSmUp ? 'auto' : 12}>
					<Button size='large' startIcon={<AssignmentTurnedInIcon />} onClick={handleSetStatusShippedClick} variant='contained' color='success' disabled>Mark as shipped</Button>
				</Grid2>
			</Grid2>}
			<Grid2 container spacing={2} mt={4}>
				<Grid2 size={12}>
					<Typography variant='body2'>
						Customer
					</Typography>
					{customer && <Typography>
						{customer.name}
					</Typography>}
				</Grid2>
				<Grid2>
					<Typography variant='body2'>
						Plan
					</Typography>
					{plan && <ImageList gap={8} cols={1} sx={{mt:1}}>
						<PlanListItemElV2
							key={plan.id}
							item={plan}
						/>
					</ImageList>}
				</Grid2>
			</Grid2>
		</CustomTabPanel>
		<CustomTabPanel value={tabValue} index={1}>
			{order && <ChatBox orderId={order.id} messages={messages} isOpen={tabValue===1} />}
		</CustomTabPanel>
		<SalesOrderAddProductDialog resource={addProductDialogSelectedResource} onSubmit={addProductHandleSubmit} onClose={() => closeAddProductDialog()} />
	</PageWrapper>;
};

export default SalesDetailView;
