import { Box } from '@mui/material';
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useOrderSubmitMutation } from '../../../services/orders';
import { ResourceListItem } from '../../../types';
import { QuickOrderCard, CreateQuickOrderCard } from './QuickOrderCard';
import { ResourcesListCard } from './ResourcesListCard';
import { PlanInfoQuickOrder } from '../types';
import { useQuickOrderCreateMutation } from '../../../services/quickOrders';
import { useAppSelector } from '../../../hooks';
import { selectMyCompany } from '../../accountSlice';
import { MyCompany } from '../../../services/accounts';

interface PlanViewResourcesTabContentProps {
	resources: ResourceListItem[];
	quickOrders: PlanInfoQuickOrder[];
	planId: number;
}

export const PlanViewResourcesTabContent = ({ resources, quickOrders, planId }: PlanViewResourcesTabContentProps) => {
	const [orderSubmitMutation, { isSuccess: isSubmitOrderSuccess, data: submitOrderData }] = useOrderSubmitMutation();
	const [quickOrderCreateMutation, { isSuccess: isCreateSuccess, data: createData }] = useQuickOrderCreateMutation();
	const navigate = useNavigate();
	const myCompany = useAppSelector(selectMyCompany) as MyCompany | null;

	useEffect(() => {
		if (isSubmitOrderSuccess) {
			navigate(`/purchases/${submitOrderData.id}`);
		}
	}, [isSubmitOrderSuccess, submitOrderData]);

	useEffect(() => {
		if (isCreateSuccess && createData) {
			navigate(`/quick-orders/${createData.id}`);
		}
	}, [isCreateSuccess, createData]);

	// Create a lookup map for resources by their ID
	const resourcesById = new Map<number, ResourceListItem>(
		resources.map((resource: ResourceListItem) => [resource.id, resource])
	);

	// Convert quickOrders into our QuickOrderCard format
	const [lists, setLists] = useState<PlanInfoQuickOrder[]>(quickOrders.map((quickOrder) => {
		// Update products, setting quantity to 0 for tools
		const updatedProducts = quickOrder.products.map(product => {
			const resource = resourcesById.get(product.resourceId);
			return {
				...product,
				quantity: (!resource || resource.isTool) ? 0 : product.quantity
			};
		});

		return {
			...quickOrder,
			products: updatedProducts,
			title: `Cart by ${quickOrder.vendor.name}`,
			image: quickOrder.vendor.imgUrl,
		};
	}));

	const handleQuantityChange = (listId: number, productId: number, newQuantity: number) => {
		setLists(prev => prev.map(list => {
			if (list.id === listId) {
				return {
					...list,
					products: list.products.map(product => {
						if (product.id === productId) {
							return {
								...product,
								quantity: newQuantity
							};
						}
						return product;
					})
				};
			}
			return list;
		}));
	};

	const handleEdit = (quickOrderId: number) => {
		navigate(`/quick-orders/${quickOrderId}`);
	};

	const handleCreate = () => {
		quickOrderCreateMutation({planId: planId});
	};

	// Convert resources into GenericItems format
	const genericItems = resources.map(resource => ({
		name: resource.name || '',
		quantity: resource.quantity || 0,
		uom: resource.uom || ''
	})) as ResourceListItem[];

	const shouldShowCreateQuickOrderCard = myCompany?.roleVendor && !lists.find(list => list.vendorId === myCompany.id);

	return (
		<Box sx={{ overflowX: 'auto' }}>
			<Box sx={{ 
				display: 'flex', 
				gap: 2,
				pb: 2,
				minWidth: 'min-content'
			}}>
				{lists.map((list, listIndex) => (
					<QuickOrderCard 
						key={list.id} 
						list={list}
						resources={resources}
						onQuantityChange={(productId, newQuantity) => handleQuantityChange(list.id, productId, newQuantity)}
						onOrderSubmit={orderSubmitMutation}
						onEdit={() => handleEdit(list.id)}
					/>
				))}
				<ResourcesListCard items={genericItems} />
				{shouldShowCreateQuickOrderCard && <CreateQuickOrderCard onCreate={handleCreate} />}
			</Box>
		</Box>
	);
};
