import React, { ReactNode } from 'react';
import { useTheme, ImageListItem, ImageListItemBar, IconButton, ImageList, useMediaQuery } from '@mui/material';
import { Favorite as FavoriteIcon, FavoriteBorder as FavoriteBorderIcon } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import dayjs from 'dayjs';
import relativeTime from 'dayjs/plugin/relativeTime';
import { PlanListItem } from '../types';

dayjs.extend(relativeTime);

interface PlanListProps {
	children: NonNullable<ReactNode>;
}

interface PlanListItemElProps {
	item: PlanListItem,
	isFavourited: boolean,
	onRemoveFavouriteBtnClick: () => void,
	onAddFavouriteBtnClick?: () => void,
}

const PlanListContainer = ({ children }: PlanListProps) => {
	const theme = useTheme();
	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));
	return <ImageList cols={isSmUp ? 3 : 1} gap={8} rowHeight={400}>{children}</ImageList>;
};

export const PlanListItemEl = ({ item, isFavourited, onRemoveFavouriteBtnClick, onAddFavouriteBtnClick}: PlanListItemElProps) => {
	const theme = useTheme();
	const navigate = useNavigate();
	const timeTillNow = dayjs(item.publishedAt).fromNow();
	const isUnavailable = !item.publishedAt;

	const handleAddFavouriteBtnClick = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
		e.stopPropagation();
		if (!onAddFavouriteBtnClick) {
			return;
		}
		onAddFavouriteBtnClick();
	};

	const handleRemoveFavouriteBtnClick = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
		e.stopPropagation();
		onRemoveFavouriteBtnClick();
	};

	return isUnavailable ? <ImageListItem>
		<ImageListItemBar
			position='top'
			title={'Content unavailable'}
			subtitle={'The author has most likely unpublished this entry'}
			actionIcon={
				<IconButton
					sx={{ color: 'rgba(255, 255, 255, 0.54)', mt:1}}
					aria-label={`info about ${item.title}`}
					onClick={(e) => handleRemoveFavouriteBtnClick(e)}
				>
					<FavoriteIcon color='primary' />
				</IconButton>
			}
		/>
	</ImageListItem> : <ImageListItem key={item.imgUrl} onClick={() => navigate(`/plans/${item.id}`)} sx={{cursor: 'pointer', overflowX:'hidden'}}>
		<img
			srcSet={`${item.imgUrl}?w=248&fit=crop&auto=format&dpr=2 2x`}
			src={`${item.imgUrl}?w=248&fit=crop&auto=format`}
			alt={item.title}
			loading="lazy"
			style={{borderRadius: '8px', boxShadow: theme.shadows[2], overflow: 'hidden'}}
		/>
		<ImageListItemBar
			title={item.title}
			subtitle={<><span>by: @{item.authorName}</span><span> &middot; {timeTillNow}</span></>}
			actionIcon={
				<IconButton
					sx={{ color: 'rgba(255, 255, 255, 0.54)', mt:1}}
					aria-label={`info about ${item.title}`}
					onClick={(e) => isFavourited ? handleRemoveFavouriteBtnClick(e) : handleAddFavouriteBtnClick(e)}
				>
					{isFavourited ? <FavoriteIcon color='primary' /> : <FavoriteBorderIcon color='disabled' />}
				</IconButton>
			}
			position='below'
		/>
	</ImageListItem>;
};


// TODO: unify or rename
interface PlanListItemElPropsV2 {
	item: PlanListItem,
}

export const PlanListItemElV2 = ({ item }: PlanListItemElPropsV2) => {
	const theme = useTheme();
	const navigate = useNavigate();
	const timeTillNow = dayjs(item.publishedAt).fromNow();

	return <ImageListItem key={item.imgUrl} onClick={() => navigate(`/plans/${item.id}`)} sx={{cursor: 'pointer', overflowX:'hidden', maxWidth: 375}}>
		<img
			srcSet={`${item.imgUrl}?w=248&fit=crop&auto=format&dpr=2 2x`}
			src={`${item.imgUrl}?w=248&fit=crop&auto=format`}
			alt={item.title}
			loading="lazy"
			style={{borderRadius: '8px', boxShadow: theme.shadows[2], overflow: 'hidden'}}
		/>
		<ImageListItemBar
			title={item.title}
			subtitle={<><span>by: @{item.authorName}</span><span> &middot; {timeTillNow}</span></>}
			position='below'
		/>
	</ImageListItem>;
};

export default PlanListContainer;
