import { PlanListItem, ResourceListItem } from '../types';
import {api} from './api';

export const plansApi = api.injectEndpoints({
	endpoints: (builder) => ({
		// TODO: rename to plansGetPublic
		plansListPublic: builder.mutation({
			query: (args: void) => ({
				url: 'plans/listPublic',
				method: 'GET',
			}),
		}),
		plansList: builder.mutation({
			query: (args: void) => ({
				url: 'plans/list',
				method: 'GET',
			}),
		}),
		planGetInfo: builder.mutation({
			query: (id: string) => ({
				url: `plans/${id}/info`,
				method: 'GET',
			}),
		}),
		planGetInfoPublic: builder.mutation({
			query: (id: string) => ({
				url: `plans/${id}/infoPublic`,
				method: 'GET',
			}),
		}),
		loadFavourites: builder.mutation<PlanListItem[], void>({
			query: () => ({
				url: 'favourites',
				method: 'GET',
			}),
			extraOptions: { maxRetries: 1 }
		}),
		addFavourite: builder.mutation<PlanListItem, {planId: number}>({
			query: (data) => ({
				url: 'favourites',
				method: 'POST',
				body: data,
			}),
			extraOptions: { maxRetries: 1 }
		}),
		removeFavourite: builder.mutation<number, number>({
			query: (planId: number) => ({
				url: `favourites/${planId}`,
				method: 'DELETE',
			}),
			extraOptions: { maxRetries: 1 }
		}),
		planUpdate: builder.mutation({
			query: ({id, ...data}) => ({
				url: `plans/${id}`,
				method: 'PUT',
				body: data,
			}),
		}),
		planDelete: builder.mutation({
			query: (planId: number) => ({
				url: `plans/${planId}`,
				method: 'DELETE',
			}),
		}),
		planAddFromYT: builder.mutation<PlanListItem, {videoId: string}>({
			query: (data) => ({
				url: 'plans/addFromYT',
				method: 'POST',
				body: data,
			}),
			extraOptions: { maxRetries: 1 }
		}),
		planPublish: builder.mutation<void, number>({
			query: (planId) => ({
				url: `plans/${planId}/publish`,
				method: 'PUT',
			}),
			extraOptions: { maxRetries: 1 }
		}),
		planUnpublish: builder.mutation<void, number>({
			query: (planId) => ({
				url: `plans/${planId}/unpublish`,
				method: 'PUT',
			}),
			extraOptions: { maxRetries: 1 }
		}),
		planResourceAdd: builder.mutation<ResourceListItem, { planId: number }>({
			query: (data) => ({
				url: `plans/${data.planId}/addResource`,
				method: 'POST',
				body: data,
			}),
			extraOptions: { maxRetries: 1 }
		}),
		planImageDelete: builder.mutation<number, number>({
			query: (imageId) => ({
				url: `planImages/${imageId}`,
				method: 'DELETE',
			}),
			extraOptions: { maxRetries: 1 }
		}),
	}),
});

export const { usePlansListPublicMutation, usePlanGetInfoMutation, useLoadFavouritesMutation, useAddFavouriteMutation, useRemoveFavouriteMutation, usePlansListMutation, usePlanUpdateMutation, usePlanAddFromYTMutation, usePlanPublishMutation, usePlanUnpublishMutation, usePlanImageDeleteMutation, usePlanDeleteMutation, usePlanGetInfoPublicMutation, usePlanResourceAddMutation } = plansApi;
