import { List, ListItem, Typography, Divider, ListItemIcon } from '@mui/material';
import React from 'react';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';

interface Props {
    subTotal: number;
    tax: number | false;
    shipping: number | false;
    total: number | false;
    isPaid?: boolean;
}

export const OrderPaymentTotals = ({subTotal, tax, shipping, total, isPaid}: Props) => <List sx={{pb:0}}>
	<ListItem>
		<Typography flex={1}>Subtotal</Typography>
		<Typography>€{subTotal.toFixed(2)}</Typography>
	</ListItem>
	{(tax || shipping || total) && <Divider />}
	{tax && <ListItem>
		<Typography flex={1}>Tax</Typography>
		<Typography>€{tax.toFixed(2)}</Typography>
	</ListItem>}
	{tax && <Divider />}
	{shipping && <ListItem>
		<Typography flex={1}>Delivery</Typography>
		<Typography>€{shipping.toFixed(2)}</Typography>
	</ListItem>}
	{shipping && total && <Divider />}
	{total && <ListItem>
		<Typography flex={1} fontWeight='bold'>Total</Typography>
		{isPaid && <ListItemIcon sx={{minWidth:'auto', pr: 1}}>
			<CheckCircleIcon color='success' />
		</ListItemIcon>}
		{isPaid && <Typography mr={2}>Paid</Typography>}
		<Typography fontWeight='bold'>€{total.toFixed(2)}</Typography>
	</ListItem>}
</List>;
