import { useTheme, useMediaQuery, Box, Typography, List, ListItemButton, ListItemAvatar, Avatar, ListItemText } from '@mui/material';
import dayjs from 'dayjs';
import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuthEffects } from '../../components/Auth';
import PageWrapper from '../../components/PageWrapper';
import { useAppSelector } from '../../hooks';
import { selectQuickOrdersListItems } from './quickOrderSlice';
import FolderIcon from '@mui/icons-material/Folder';
import { useQuickOrdersListMutation } from '../../services/quickOrders';

const QuickOrdersView = () => {
	useAuthEffects();

	const theme = useTheme();
	const navigate = useNavigate();
	const [quickOrdersListMutation] = useQuickOrdersListMutation();
	const listItems = useAppSelector(selectQuickOrdersListItems);

	useEffect(() => {
		quickOrdersListMutation({});
	}, []);

	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));

	const isEmptyView = !listItems.length;

	return <PageWrapper disableFooter={isEmptyView} noScroll={isEmptyView}>
		{isEmptyView && <Box flex={1} display='flex' alignItems='center' justifyContent='center'>
			<Typography>No quick orders yet !</Typography>
		</Box>}
		{!isEmptyView && <Box>
			<Typography variant='body2' sx={{mt:2}}>Quick orders</Typography>
			<List>
				{listItems.map((listItem, i) => (
					<ListItemButton key={i} onClick={() => navigate(`/quick-orders/${listItem.id}`)}>
						<ListItemAvatar>
							<Avatar src={listItem.plan.imgUrl} variant='square' sx={{ width: 96, height: 96, mr: 2, borderRadius: 2 }}>
								<FolderIcon />
							</Avatar>
						</ListItemAvatar>
						<ListItemText
							primary={<span>{listItem.itemsCount} items ordered by {listItem.plan.title}</span>}
							secondary={dayjs(listItem.createdAt).format('MMMM D, HH:mm')}
						/>
					</ListItemButton>
				))}
			</List> 
		</Box>}
	</PageWrapper>;
};

export default QuickOrdersView;
