import { Avatar, FormControlLabel, ImageListItem, ImageList, Switch, TextField, useTheme } from '@mui/material';
import React, { useMemo, useState } from 'react';
import AccountBoxOutlinedIcon from '@mui/icons-material/AccountBoxOutlined';
import { PlanImageItem } from '../types';
import { srcset } from '../common/util';

export enum SimpleFormPropertyType {
    String = 'string',
	Number = 'number',
    Text = 'text',
    Videos = 'videos',
    Image = 'image',
    Images = 'images',
    Toggle = 'toggle',
}

interface TemplatePropertyBase<T> {
	name: keyof T,
	label: string,
}

interface TemplatePropertyString<T> extends TemplatePropertyBase<T> {
	type: SimpleFormPropertyType.String,
	required?: boolean,
}

interface TemplatePropertyNumber<T> extends TemplatePropertyBase<T> {
	type: SimpleFormPropertyType.Number,
	required?: boolean,
}

interface TemplatePropertyText<T> extends TemplatePropertyBase<T> {
	type: SimpleFormPropertyType.Text,
	required?: boolean,
}

interface TemplatePropertyToggle<T> extends TemplatePropertyBase<T> {
	type: SimpleFormPropertyType.Toggle,
}

interface TemplatePropertyImage<T> extends TemplatePropertyBase<T> {
	type: SimpleFormPropertyType.Image,
}

interface TemplatePropertyImages<T> extends TemplatePropertyBase<T> {
	type: SimpleFormPropertyType.Images,
}

export type SimpleFormTemplateProperty<T> = TemplatePropertyString<T> | TemplatePropertyNumber<T> | TemplatePropertyText<T> | TemplatePropertyToggle<T> | TemplatePropertyImage<T> | TemplatePropertyImages<T>;

interface FormFieldProps<T> { 
    property: SimpleFormTemplateProperty<T>;
    value: any;
    onChange?: (property: string, value: any) => void;
}

export function useSimpleForm<T>(template: SimpleFormTemplateProperty<T>[]) {
	const [formData, setFormData] = useState<Partial<T>>({});
	const [initialData, setInitialData] = useState<Partial<T>>({});

	function resetFormData<T>(data: Partial<T> = {}) {
		if (!data) {
			return;
		}
		const newFormData = template.reduce((acc, property) => ({...acc, [property.name]: (data as any)[property.name]}), {} as Partial<T>);
		setInitialData(newFormData as any);
		setFormData(newFormData as any);
	}

	const handleFormFieldChange = (name: string, value: any) => {
		setFormData({
			...formData,
			[name]: value
		});
	};
    
	const hasFormChanged = useMemo(() => {
		if (!initialData) {
			return false;
		}
		return template.find((property) => formData[property.name] !== initialData[property.name]) ? true : false;
	}, [formData, initialData]);

	return {formData, handleFormFieldChange, resetFormData, hasFormChanged};
}

export function SimpleFormField<T>({ property, value, onChange }: FormFieldProps<T>) {
	const theme = useTheme();
	const handleChange = (e: React.ChangeEvent<any>) => {
		if (onChange) {
			onChange(e.target.name, e.target.value);
		}
	};

	switch (property.type) {
	case SimpleFormPropertyType.String: {
		return <TextField 
			required={property.required} 
			autoComplete='off' 
			name={property.name as string} 
			label={property.label} 
			variant="filled" 
			fullWidth 
			value={value} 
			onChange={handleChange}
			// defaultValue={property.defaultValue}
		/>;
	}
	case SimpleFormPropertyType.Number: {
		return <TextField 
			autoComplete='off' 
			type='number' name={property.name as string} 
			label={property.label} 
			variant="filled" 
			fullWidth 
			value={value} 
			onChange={handleChange}
			required={property.required} 
			// defaultValue={property.defaultValue}
		/>;
	}
	case SimpleFormPropertyType.Text: {
		return <TextField
			name={property.name as string}
			id="outlined-textarea"
			label={property.label}
			placeholder="A happy builder"
			multiline
			variant='filled'
			rows={4}
			fullWidth
			value={value ?? ''}
			onChange={handleChange}
			required={property.required}
			// defaultValue={property.defaultValue}
		/>;
	}
	case SimpleFormPropertyType.Toggle: {
		return <FormControlLabel 
			control={<Switch 
				checked={value} 
				onChange={onChange ? () => onChange(property.name as string, !value) : undefined} />} 
			label={property.label} 
		/>;
	}
	case SimpleFormPropertyType.Image: {
		return <Avatar src={value ?? undefined} sx={{ bgcolor: theme.palette.action.hover, color: theme.palette.text.primary, width: 196, height: 196, fontSize: 40, borderRadius: '5px' }} variant="square">
			<AccountBoxOutlinedIcon color='disabled' fontSize='large' />
		</Avatar>;
	}
	case SimpleFormPropertyType.Images: {
		const values: PlanImageItem[] = value ?? [];
		return <ImageList
			variant="quilted"
			cols={4}
			rowHeight={100}
			sx={{m:0}}
		>
			{values.map((productImage, i) => {
				const colsRows = i === 0 ? 4 : 2;
				return (
					<ImageListItem key={productImage.imgUrl} cols={colsRows} rows={colsRows} sx={{borderRadius: 2, overflow: 'hidden', boxShadow: 3}}>
						<img
							{...srcset(productImage.imgUrl, 250, colsRows * 1, colsRows * 1)}
							// alt={productImage.alt}
							loading="lazy"
							style={{overflow:'hidden'}}
						/>
					</ImageListItem>
				);})}
		</ImageList>;
	}
	default: {
		return <span>{JSON.stringify(value)}</span>;
		// throw new Error('invalid property type')
	}  
	}
}
