import {createSelector, createSlice, PayloadAction} from '@reduxjs/toolkit';
import {RootState} from '../../store';
import { plansApi } from '../../services/plans';
import { PlanListItem, ResourceListItem, OrderProduct } from '../../types';
import { planResourcesApi } from '../../services/planResources';
import { PlanDetails } from './types';

interface PlanState {
	planListItems: PlanListItem[];
	planDetails: PlanDetails | null;
	owned: PlanListItem[];
	favourites: PlanListItem[];
}

const initialState: PlanState = {
	planListItems: [],
	planDetails: null,
	owned: [],
	favourites: [],
};

export const planSlice = createSlice({
	name: 'plans',
	initialState,
	reducers: {
		planListItemsSetState(state: PlanState, action) {
			state.planListItems = action.payload;
			return state;
		},
		planOwnedSetState(state: PlanState, action) {
			state.owned = action.payload;
			return state;
		},
		planDetailsSetState(state: PlanState, action) {
			state.planDetails = action.payload;
			return state;
		},
		planResourceAddItem(state: PlanState, action) {
			const newResources = [...state.planDetails!.resources, action.payload];
			state.planDetails = {...state.planDetails!, resources: newResources};
		},
		planResourceRemoveItem(state: PlanState, action) {
			const newResources = state.planDetails!.resources.filter(({ id }) => id !== action.payload);
			state.planDetails = {...state.planDetails!, resources: newResources};
		}
	},
	extraReducers: (builder) => {
		builder
			.addMatcher(plansApi.endpoints.plansListPublic.matchFulfilled, (state: PlanState, action: PayloadAction<PlanListItem[]>) => {
				planSlice.caseReducers.planListItemsSetState(state,	planSlice.actions.planListItemsSetState(action.payload));
			})
			.addMatcher(plansApi.endpoints.planGetInfoPublic.matchFulfilled, (state: PlanState, action: PayloadAction<PlanDetails>) => {
				planSlice.caseReducers.planDetailsSetState(state, planSlice.actions.planDetailsSetState(action.payload));
			})
			.addMatcher(plansApi.endpoints.planGetInfo.matchFulfilled, (state: PlanState, action: PayloadAction<PlanDetails>) => {
				planSlice.caseReducers.planDetailsSetState(state, planSlice.actions.planDetailsSetState(action.payload));
			})
			.addMatcher(plansApi.endpoints.planResourceAdd.matchFulfilled, (state: PlanState, action: PayloadAction<ResourceListItem>) => {
				planSlice.caseReducers.planResourceAddItem(state, planSlice.actions.planResourceAddItem(action.payload));
			})
			.addMatcher(planResourcesApi.endpoints.planResourceDelete.matchFulfilled, (state: PlanState, action: PayloadAction<number>) => {
				planSlice.caseReducers.planResourceRemoveItem(state, planSlice.actions.planResourceRemoveItem(action.payload));
			})
			.addMatcher(plansApi.endpoints.plansList.matchFulfilled, (state: PlanState, action: PayloadAction<PlanListItem[]>) => {
				planSlice.caseReducers.planOwnedSetState(state,	planSlice.actions.planOwnedSetState(action.payload));
			})
			.addMatcher(plansApi.endpoints.loadFavourites.matchFulfilled, (state, action) => {
				state.favourites = action.payload;
			})
			.addMatcher(plansApi.endpoints.addFavourite.matchFulfilled, (state, action) => {
				state.favourites = [action.payload, ...state.favourites];
			})
			.addMatcher(plansApi.endpoints.removeFavourite.matchFulfilled, (state, action) => {
				state.favourites = state.favourites.filter(({ id }) => id !== action.payload);
			})
			.addMatcher(plansApi.endpoints.planImageDelete.matchFulfilled, (state, action) => {
				const images = (state.planDetails?.images ?? []).filter(({ id }) => id !== action.payload);
				state.planDetails = {
					...state.planDetails,
					images,
				} as PlanDetails;
			})
			.addMatcher(plansApi.endpoints.planAddFromYT.matchFulfilled, (state, action) => {
				state.owned = [action.payload, ...state.owned];
			})
			// the following is only necessary for the "reset" button to work properly after an update has been submitted, because the formData will be based on the entity that is loaded into memory. Should be removed if edit form is moved into a modal.
			.addMatcher(plansApi.endpoints.planUpdate.matchFulfilled, (state, action: PayloadAction<PlanDetails>) => {
				state.planDetails = {...state.planDetails, ...action.payload} as PlanDetails;
			});
	},
});

export const {
	planListItemsSetState,
	planDetailsSetState,
} = planSlice.actions;

export const selectPlansState = (state: RootState) => state.plans;

export const selectPlanListItems = createSelector([selectPlansState], (state) => state.planListItems);

export const selectPlanDetails = createSelector([selectPlansState], (state) => state.planDetails ?? null);

export const selectPlanAuthor = createSelector([selectPlansState], (state) => state.planDetails?.author ?? null);

export const selectPlanVendors = createSelector([selectPlansState], (state) => state.planDetails?.vendors ?? []);

export const selectPlanImages = createSelector([selectPlansState], (state) => state.planDetails?.images ?? []);

export const selectPlanVideos = createSelector([selectPlansState], (state) => state.planDetails?.videos ?? []);

const selectPlanResources = createSelector([selectPlansState], (state) => state.planDetails?.resources ?? []);

export const selectPlanResourcesMaterials = createSelector([selectPlanResources], (resources) => resources.filter(({isTool}) => !isTool));

export const selectPlanResourcesTools = createSelector([selectPlanResources], (resources) => resources.filter(({isTool}) => isTool));

export const selectFavourites = createSelector([selectPlansState], (state: PlanState) => state.favourites);

export const selectFavouriteIds = createSelector([selectFavourites], (favourites: PlanListItem[]) => favourites.reduce((acc, fav) => acc.add(fav.id), new Set<number>()));

export const selectPlansOwned = createSelector([selectPlansState], (state: PlanState) => state.owned);

export const selectPlanQuickOrders = createSelector([selectPlansState], (state) => state.planDetails?.quickOrders ?? []);

export default planSlice.reducer;
