import React, { useEffect } from 'react';
import PageWrapper from '../components/PageWrapper';
import { useAuthEffects } from '../components/Auth';
import { Divider, Grid2 } from '@mui/material';
import { usePlansListPublicMutation } from '../services/plans';
import { useAppSelector } from '../hooks';
import { selectFavouriteIds, selectPlanListItems } from './plans/planSlice';
import { useAddFavouriteMutation, useRemoveFavouriteMutation } from '../services/plans';
import { selectUserProfile } from './accountSlice';
import PlanList, {PlanListItemEl} from '../components/PlanList';

const DiscoverView = () => {
	useAuthEffects();

	const [plansListPublicMutation] = usePlansListPublicMutation();
	const [addFavouriteMutation] = useAddFavouriteMutation();
	const [removeFavouriteMutation] = useRemoveFavouriteMutation();
	const listItems = useAppSelector(selectPlanListItems);
	const userProfile = useAppSelector(selectUserProfile);
	const favouriteIds = useAppSelector(selectFavouriteIds);

	useEffect(() => {
		plansListPublicMutation();
	}, []);

	const handleAddFavouriteBtnClick = (planId: number) => {
		if (!userProfile) {
			return;
		}
		addFavouriteMutation({ planId });
	};

	const handleRemoveFavouriteBtnClick = (planId: number) => {
		removeFavouriteMutation(planId);
	};

	return <PageWrapper>
		<Grid2 container spacing={8}>
			<Grid2 size={12}>
				<Divider sx={{mt: 1}}/>
				<PlanList>
					{listItems.map((item) => <PlanListItemEl
						key={item.id}
						item={item}
						isFavourited={favouriteIds.has(item.id)}
						onAddFavouriteBtnClick={() => handleAddFavouriteBtnClick(item.id)}
						onRemoveFavouriteBtnClick={() => handleRemoveFavouriteBtnClick(item.id)}
					/>)}
				</PlanList>
			</Grid2>
		</Grid2>
	</PageWrapper>;
};

export default DiscoverView;
