import { Box, Typography, Button } from '@mui/material';
import React, { useState, useRef, useEffect } from 'react';
import Linkify from 'react-linkify';

export const ExpandableParagraph = ({ text }: { text: string }) => {
	const [isExpanded, setIsExpanded] = useState(false);
	const [isOverflowing, setIsOverflowing] = useState(false);
	const paragraphRef = useRef<HTMLParagraphElement>(null);

	useEffect(() => {
		if (paragraphRef.current) {
			setIsOverflowing(paragraphRef.current.scrollHeight > paragraphRef.current.clientHeight);
		}
	}, [text]);

	return <Box mb={3}><Typography ref={paragraphRef} sx={{whiteSpace:'pre-line', '& > p': {m: 0}, 
		overflow: 'hidden',
		display: '-webkit-box',
		WebkitLineClamp: isExpanded ? 'unset' : '10',
		WebkitBoxOrient: 'vertical',
		textOverflow: 'ellipsis',
	}}>
		<Linkify
			componentDecorator={(href, text, key) => (
				<a
					key={key}
					href={href}
					target="_blank"
					rel="noopener noreferrer"
					style={{ color: '#007bff', textDecoration: 'underline' }}
				>
					{text}
				</a>
			)}
		>
			{text}
		</Linkify>
	</Typography>
	{isOverflowing && (
		<Button onClick={() => setIsExpanded(!isExpanded)} sx={{mt:1}}>
			{isExpanded ? 'Show Less' : 'Show More'}
		</Button>)}
	</Box>;
};
