import React from 'react';
import PageWrapper from '../components/PageWrapper';
import { useAuthEffects } from '../components/Auth';
import { Divider, Grid2, Box, Typography } from '@mui/material';
import { useRemoveFavouriteMutation } from '../services/plans';
import { useAppSelector } from '../hooks';
import { selectFavourites } from './plans/planSlice';
import PlanList, {PlanListItemEl} from '../components/PlanList';

const DiscoverView = () => {
	useAuthEffects();
	const [removeFavouriteMutation] = useRemoveFavouriteMutation();
	const listItems = useAppSelector(selectFavourites);

	const handleRemoveFavouriteBtnClick = (planId: number) => {
		removeFavouriteMutation(planId);
	};

	return <PageWrapper disableFooter={!listItems.length} noScroll={!listItems.length}>
		{!listItems.length ? <Box flex={1} display='flex' alignItems='center' justifyContent='center'>
			<Typography>Keep discovering !</Typography>
		</Box> :
			<Grid2 container spacing={8}>
				<Grid2 size={12}>
					<Divider sx={{mt: 1}}/>
					<PlanList>
						{listItems.map((item) => <PlanListItemEl 
							key={item.id}
							item={item}
							isFavourited
							onRemoveFavouriteBtnClick={() => handleRemoveFavouriteBtnClick(item.id)}
						/>)}
					</PlanList>
				</Grid2>
			</Grid2>}
	</PageWrapper>;
};

export default DiscoverView;
