import React, { useEffect, useMemo, useState } from 'react';
import PageWrapper from '../../components/PageWrapper';
import { Box, Button, Card, Dialog, DialogActions, DialogContent, DialogTitle, Grid2, IconButton, ImageList, ImageListItem, ImageListItemBar, useMediaQuery, useTheme } from '@mui/material';
import { Link, useNavigate, useParams } from 'react-router-dom';
import { usePlanDeleteMutation, usePlanGetInfoMutation, usePlanImageDeleteMutation, usePlanPublishMutation, usePlanResourceAddMutation, usePlanUnpublishMutation, usePlanUpdateMutation } from '../../services/plans';
import { useAppSelector } from '../../hooks';
import { useAuthEffects } from '../../components/Auth';
import { selectPlanDetails } from './planSlice';
import { ResourceListItem } from '../../types';
import { SimpleFormField, SimpleFormPropertyType, SimpleFormTemplateProperty, useSimpleForm } from '../../components/SimpleForm';
import { srcset } from '../../common/util';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';
import { ResourcesListItem } from './components/ResourceListItem';
import { usePlanResourceDeleteMutation } from '../../services/planResources';
import { PlanDetails } from './types';

const PLAN_EDIT_FORM_TEMPLATE: SimpleFormTemplateProperty<PlanDetails>[] = [{
	name: 'title',
	label: 'Title',
	type: SimpleFormPropertyType.String,
}, {
	name: 'description',
	label: 'Description',
	type: SimpleFormPropertyType.Text,
}, {
	name: 'materialsCost',
	label: 'Estimated materials cost',
	type: SimpleFormPropertyType.String,
}, {
	name: 'timeCost',
	label: 'Estimated time cost',
	type: SimpleFormPropertyType.String,
}];

const PLAN_ADD_RESOURCE_FORM_TEMPLATE: SimpleFormTemplateProperty<ResourceListItem>[] = [{
	name: 'name',
	label: 'Name',
	type: SimpleFormPropertyType.String,
	required: true
},{
	name: 'description',
	label: 'Secondary text',
	type: SimpleFormPropertyType.String,
},{
	name: 'quantity',
	label: 'Quantity',
	type: SimpleFormPropertyType.Number,
	required: true
},{
	name: 'uom',
	label: 'Uom',
	type: SimpleFormPropertyType.String,
	required: true,
},{
	name: 'isTool',
	label: 'Is a Tool',
	type: SimpleFormPropertyType.Toggle,
}];

const PLAN_ADD_RESOURCE_FORM_INITIAL_STATE = {
	uom: 'pcs',
	quantity: '1',
};

interface PlanViewResourceListProps {
	resources: ResourceListItem[];
	onAddClick: () => void;
	onDeleteClick: (resourceId: number) => void;
}

const PlanEditViewResourceList = ({resources, onAddClick, onDeleteClick}: PlanViewResourceListProps) => {
	return <>
		<Grid2 container spacing={1} mt={1}>
			{resources.map((resource, i) => {
				return <Grid2 size={12} key={i}>
					<Card sx={{borderRadius: 2}} elevation={0}>
						<ResourcesListItem
							resource={resource}
							onDeleteClick={() => onDeleteClick(resource.id)}
						/>
					</Card>
				</Grid2>;
			})}
		</Grid2>
		<Box display='flex' alignItems='center' justifyContent='space-between' m={1}>
			<Button startIcon={<AddIcon />} color='inherit' onClick={onAddClick}>Add item</Button>
		</Box>
	</>;
};

export default function PlanEditView() {
	useAuthEffects();

	const { id } = useParams();
	const theme = useTheme();
	const [planGetInfoMutation] = usePlanGetInfoMutation();
	const [planUpdateMutation] = usePlanUpdateMutation();
	const plan = useAppSelector(selectPlanDetails);
	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));
	const {formData, resetFormData, handleFormFieldChange, hasFormChanged} = useSimpleForm<PlanDetails>(PLAN_EDIT_FORM_TEMPLATE);
	const {formData: addResourceFormData, resetFormData: addResourceResetFormData, handleFormFieldChange: addResourceHandleFormFieldChange, hasFormChanged: addResourceHasFormChanged} = useSimpleForm<ResourceListItem>(PLAN_ADD_RESOURCE_FORM_TEMPLATE);
	const [isPublished, setIsPublished] = useState<boolean>(false);
	const [planPublishMutation] = usePlanPublishMutation();
	const [planUnpublishMutation] = usePlanUnpublishMutation();
	const [planImageDeleteMutation] = usePlanImageDeleteMutation();
	const [planDeleteMutation, {isSuccess: isDeleteSuccess}] = usePlanDeleteMutation();
	const [planResourceAddMutation] = usePlanResourceAddMutation();
	const [planResourceDeleteMutation] = usePlanResourceDeleteMutation();
	const [isAddResourceDialogOpen, setIsAddResourceDialogOpen] = useState(false);
	const navigate = useNavigate();

	const resources = useMemo(() => plan ? plan.resources : [], [plan]);

	useEffect(() => {
		if (id) {
			planGetInfoMutation(id);
		}
	}, [id]);

	useEffect(() => {
		if (plan) {
			resetFormData(plan);
			setIsPublished(Boolean(plan.publishedAt));
		}
	}, [plan]);

	useEffect(() => {
		if (isDeleteSuccess) {
			navigate('/plans');
		}
	}, [isDeleteSuccess]);

	const handleFormSubmit = (e: React.FormEvent<HTMLFormElement>) => {
		e.preventDefault();
		planUpdateMutation({...formData, id});
		resetFormData({...formData, id});
	};

	const handleIsPublishedChange = () => {
		if (isPublished) {
			planUnpublishMutation(plan!.id);
		} else {
			planPublishMutation(plan!.id);
		}
		setIsPublished(!isPublished);
	};

	const handleDeleteImageClick = (imageId: number) => {
		planImageDeleteMutation(imageId);
	};

	const handleDeleteClick = () => {
		if (plan) {
			planDeleteMutation(plan.id);
		}
	};

	const handleResourceAdd = () => {
		if (plan) {
			planResourceAddMutation({ 
				...addResourceFormData,
				planId: plan.id,
			});
		}
		setIsAddResourceDialogOpen(false);
	};

	const addResourceHandleFormSubmit = (e: React.FormEvent<HTMLFormElement>) => {
		e.preventDefault();
		handleResourceAdd();
	};

	const handleResourceDelete = (resourceId: number) => {
		planResourceDeleteMutation(resourceId);
	};

	useEffect(() => {
		if (isAddResourceDialogOpen) {
			addResourceResetFormData(PLAN_ADD_RESOURCE_FORM_INITIAL_STATE);
		}
	}, [isAddResourceDialogOpen]);

	return <PageWrapper>
		{plan && <form onSubmit={handleFormSubmit}>
			<Grid2 container spacing={1}>
				<Grid2 size={isSmUp ? 6 : 12} container spacing={1} alignContent='baseline'>
					{PLAN_EDIT_FORM_TEMPLATE.map((property) => <Grid2 key={property.name} size={12}>
						<SimpleFormField property={property} value={formData[property.name] ?? ''} onChange={handleFormFieldChange} />
					</Grid2>)}
					<Grid2 size={12}>
						<Button disabled={!hasFormChanged} variant='text' color='inherit' onClick={() => resetFormData(plan)}>Reset</Button>
						&nbsp;
						<Button type='submit' disabled={!hasFormChanged} variant='contained'>Save</Button>
					</Grid2>
					<Grid2 size={12}>
						<SimpleFormField property={{name: 'isPublished', label: 'Is published', type: SimpleFormPropertyType.Toggle}} value={isPublished} onChange={handleIsPublishedChange} />
					</Grid2>
					<Grid2 size={12}>
						<Link to={`/plans/${plan.id}`}>To public view</Link>
					</Grid2>
					<Grid2 size={12}>
						<Button variant='contained' color='error' onClick={handleDeleteClick}>Delete</Button>
					</Grid2>
				</Grid2>
				<Grid2 size={isSmUp ? 6 : 12}>
					<ImageList
						variant="quilted"
						cols={4}
						rowHeight={100}
						sx={{m:0}}
					>
						{plan.images.map((productImage, i) => {
							const colsRows = i === 0 ? 4 : 2;
							return (
								<ImageListItem key={productImage.imgUrl} cols={colsRows} rows={colsRows} sx={{borderRadius: 2, overflow: 'hidden', boxShadow: 3}}>
									<img
										{...srcset(productImage.imgUrl, 250, colsRows * 1, colsRows * 1)}
										alt={plan.title}
										loading="lazy"
										style={{overflow:'hidden'}}
									/>
									<ImageListItemBar
										actionIcon={
											<IconButton
												sx={{ color: 'rgba(255, 255, 255, 0.54)' }}
												aria-label={`info about ${'ho'}`}
												onClick={() => handleDeleteImageClick(productImage.id)}
											>
												<DeleteIcon />
											</IconButton>
										}
									/>
								</ImageListItem>
							);})}
					</ImageList>
				</Grid2>
			</Grid2>
		</form>}
		<PlanEditViewResourceList 
			resources={resources}
			onAddClick={() => setIsAddResourceDialogOpen(true)}
			onDeleteClick={handleResourceDelete}
		/>
		<Dialog open={isAddResourceDialogOpen} onClose={() => setIsAddResourceDialogOpen(false)}>
			<DialogTitle>Add resource</DialogTitle>
			<form onSubmit={addResourceHandleFormSubmit}>
				<DialogContent>
					<Grid2 size={12} container spacing={1} alignContent='baseline'>
						{PLAN_ADD_RESOURCE_FORM_TEMPLATE.map((property) => <Grid2 key={property.name} size={12}>
							<SimpleFormField property={property} value={addResourceFormData[property.name] ?? ''} onChange={addResourceHandleFormFieldChange} />
						</Grid2>)}
					</Grid2>
				</DialogContent>
				<DialogActions>
					<Button onClick={() => setIsAddResourceDialogOpen(false)}>Cancel</Button>
					<Button type='submit' disabled={!addResourceHasFormChanged} variant='contained'>Save</Button>
				</DialogActions>
			</form>
		</Dialog>
	</PageWrapper>;
}
