import { api } from './api';

export const quickOrdersApi = api.injectEndpoints({
	endpoints: (builder) => ({
		quickOrdersList: builder.mutation({
			query: () => ({
				url: '/quickOrders/list',
				method: 'GET',
			}),
		}),
		quickOrderCreate: builder.mutation({
			query: (data: {planId: number}) => ({
				url: '/quickOrders/create',
				method: 'POST',
				body: data,
			}),
		}),
		quickOrderInfo: builder.mutation({
			query: (id: string) => ({
				url: `/quickOrders/${id}/info`,
				method: 'GET',
			}),
		}),
		quickOrderSubmitOffer: builder.mutation({
			query: ({ orderId, data }: { orderId: string, data: any }) => ({
				url: `/quickOrders/${orderId}/submit-offer`,
				method: 'POST',
				body: data,
			}),
		}),
		quickOrderCancelOffer: builder.mutation({
			query: ({ orderId, data }: { orderId: string, data: any }) => ({
				url: `/quickOrders/${orderId}/cancel-offer`,
				method: 'POST',
				body: data,
			}),
		}),
		quickOrderAddExistingProduct: builder.mutation({
			query: (payload: { quickOrderId: number, productId: number, quantity: number }) => ({
				url: `/quickOrders/${payload.quickOrderId}/addExistingProduct`,
				method: 'POST',
				body: payload,
			}),
		}),
		quickOrderAddNewProduct: builder.mutation({
			query: (payload: { quickOrderId: number, productId: number, quantity: number, name: string, sku: string, uom: string }) => ({
				url: `/quickOrders/${payload.quickOrderId}/addNewProduct`,
				method: 'POST',
				body: payload,
			}),
		}),
		quickOrderDeleteProduct: builder.mutation({
			query: (orderProductId: string) => ({
				url: `/quickOrderProducts/${orderProductId}`,
				method: 'DELETE',
			}),
		}),
		quickOrderRemove: builder.mutation({
			query: (orderId: string) => ({
				url: `/quickOrders/${orderId}`,
				method: 'DELETE',
			}),
		}),
	}),
});

export const {
	useQuickOrdersListMutation,
	useQuickOrderCreateMutation,
	useQuickOrderInfoMutation,
	useQuickOrderSubmitOfferMutation,
	useQuickOrderCancelOfferMutation,
	useQuickOrderAddExistingProductMutation,
	useQuickOrderAddNewProductMutation,
	useQuickOrderDeleteProductMutation,
	useQuickOrderRemoveMutation,
} = quickOrdersApi;
