import {api} from './api';

export const planResourcesApi = api.injectEndpoints({
	endpoints: (builder) => ({
		planResourceDelete: builder.mutation({
			query: (planResourceId: number) => ({
				url: `planResources/${planResourceId}`,
				method: 'DELETE',
			}),
		}),
	}),
});

export const { usePlanResourceDeleteMutation } = planResourcesApi;
