import qs from 'qs';
import {api} from './api';

export const productsApi = api.injectEndpoints({
	endpoints: (builder) => ({
		productListLoad: builder.mutation({
			query: (args: void) => ({
				url: 'products',
				method: 'GET',
			}),
		}),
		productsFind: builder.mutation({
			query: (findOptions: object) => ({
				url: `products?${qs.stringify(findOptions)}`,
				method: 'GET',
			}),
		}),
	}),
});

export const {useProductListLoadMutation, useProductsFindMutation} = productsApi;
