import {createSlice, createSelector} from '@reduxjs/toolkit';
import {accountsApi, MyAccount, MyCompany} from '../services/accounts';
import {removeAccessToken, storeAccessToken} from '../cookies';
import {RootState} from '../store';

export interface AccountState {
	token: string | null;
	account: MyAccount | null;
	company: MyCompany | null;
}

const initialState: AccountState = {
	token: null,
	account: null,
	company: null,
};

const accountSlice = createSlice({
	name: 'account',
	initialState,
	reducers: {
		tokenSetState(state, action) {
			state.token = action.payload;
			return state;
		},
		resetState() {
			removeAccessToken();
			return initialState;
		},
	},
	extraReducers: (builder) => {
		builder
			.addMatcher(accountsApi.endpoints.swapToken.matchFulfilled, (state, action) => {
				state.token = action.payload.token;
				storeAccessToken(action.payload.token);
			})
			.addMatcher(accountsApi.endpoints.getMyAccount.matchFulfilled, (state, action) => {
				const {company, ...account} = action.payload;
				state.account = account;
				state.company = company;
			})
			.addMatcher(accountsApi.endpoints.getMyAccount.matchRejected, accountSlice.caseReducers.resetState) // triggers token swap
			.addMatcher(accountsApi.endpoints.updateMyCompany.matchFulfilled, (state, action) => {
				state.company = {...state.company, ...action.payload} as MyCompany;
			});
	},
});

export const {
	tokenSetState,
	resetState,
} = accountSlice.actions;

const selectAccountSlice = (state: RootState) => state.account;

export const selectAccessToken = createSelector([selectAccountSlice], (state: AccountState) => state.token);

export const selectUserProfile = createSelector([selectAccountSlice], (state: AccountState) => state.account);

export const selectMyCompany = createSelector([selectAccountSlice], (state: AccountState) => state.company);

export const selectIsAuthenticated = createSelector([selectAccountSlice], (state: AccountState) => state.account ? true : false);

export default accountSlice.reducer;
