import React, { useEffect } from 'react';
import PageWrapper from '../../components/PageWrapper';
import { useAuthEffects } from '../../components/Auth';
import { useTheme, Avatar, Box, List, ListItemAvatar, ListItemButton, ListItemText, Typography } from '@mui/material';
import FolderIcon from '@mui/icons-material/Folder';
import { useNavigate } from 'react-router-dom';
import { useOrderListLoadMutation } from '../../services/orders';
import { useAppSelector } from '../../hooks';
import dayjs from 'dayjs';
import { selectOrdersListItems } from './orderSlice';
import { ListViewStatusStepper } from './components/Stepper';
import { OrderListItem, OrderStatus } from '../../types';

const OrdersView = () => {
	useAuthEffects();

	const theme = useTheme();
	const navigate = useNavigate();
	const [orderListLoadMutation] = useOrderListLoadMutation();
	const orders = useAppSelector(selectOrdersListItems);

	useEffect(() => {
		orderListLoadMutation();
	}, []);

	return <PageWrapper disableFooter={!orders.length} noScroll={!orders.length}>
		{!orders.length ? <Box flex={1} display='flex' alignItems='center' justifyContent='center'>
			<Typography>No orders yet !</Typography>
		</Box> :
			<Box>
				<Typography variant='body2' sx={{mt:2}}>My orders</Typography>
				<List>
					{orders.map((order: OrderListItem, i: number) => (
						<ListItemButton key={i} onClick={() => navigate(`/purchases/${order.id}`)}>
							<ListItemAvatar>
								<Avatar src={order.vendorImgUrl} variant='square' sx={{ width: 96, height: 96, mr: 2, borderRadius: 2 }}>
									<FolderIcon />
								</Avatar>
							</ListItemAvatar>
							<ListItemText
								primary={<span>{order.itemsCount} items from {order.vendorName}</span>}
								secondary={dayjs(order.createdAt).format('MMMM D, HH:mm')}
							/>
							<Box justifyItems='center'>
								<Box>
									{order.status}
								</Box>
								<ListViewStatusStepper orderStatus={order.status} isCancelled={[OrderStatus.Cancelled, OrderStatus.Rejected].includes(order?.status)} />
							</Box>
						</ListItemButton>
					))}
				</List> 
			</Box>}
	</PageWrapper>;
};

export default OrdersView;
