import { QuickOrderProduct } from '../types';
import {api} from './api';

export type OrderSubmitData = {
	quickOrderId: number;
	items: Pick<QuickOrderProduct, 'id' | 'quantity'>[];
};

export const ordersApi = api.injectEndpoints({
	endpoints: (builder) => ({
		orderSubmit: builder.mutation({
			query: (data: OrderSubmitData) => ({
				url: 'orders/submitOrder',
				body: data,
				method: 'POST',
			}),
		}),
		orderSubmitCustomOrder: builder.mutation({
			query: (data: object) => ({
				url: 'orders/submitCustomOrder',
				body: data,
				method: 'POST',
			}),
		}),
		orderListLoad: builder.mutation({
			query: (args: void) => ({
				url: 'orders/list',
				method: 'GET',
			}),
		}),
		orderDetailsLoad: builder.mutation({
			query: (orderId: string) => ({
				url: `orders/${orderId}/details`,
				method: 'GET',
			}),
		}),
		orderCancel: builder.mutation({
			query: (orderId: string) => ({
				url: `orders/${orderId}/cancel`,
				method: 'POST',
			}),
		}),
		orderMockPaymentCompleted: builder.mutation({
			query: (orderId: string) => ({
				url: `orders/${orderId}/paymentCompleted`,
				method: 'POST',
			}),
		}),
	}),
});

export const {useOrderSubmitMutation, useOrderSubmitCustomOrderMutation, useOrderCancelMutation, useOrderListLoadMutation, useOrderDetailsLoadMutation, useOrderMockPaymentCompletedMutation} = ordersApi;
