import { TAX_RATE } from '../constants';

export const calculateOrderTotals = (products: object & {pricePerUnit: string, quantity: number}[]) => {
	const subTotal = products.reduce((acc, {pricePerUnit, quantity}) => acc+= Number(pricePerUnit)*Number(quantity), 0);
	const tax = (TAX_RATE * Number(subTotal));
	const shipping = 2.5;
	const total = subTotal + tax + shipping;
	return {subTotal, tax, shipping, total};
};

export function srcset(image: string, size: number, rows = 1, cols = 1) {
	return {
		src: `${image}?w=${size * cols}&h=${size * rows}&fit=crop&auto=format`,
		srcSet: `${image}?w=${size * cols}&h=${
			size * rows
		}&fit=crop&auto=format&dpr=2 2x`,
	};
}
