import { alpha, Avatar, Box, IconButton, ListItemAvatar, ListItemText, useTheme } from '@mui/material';
import { OrderProduct } from '../types';
import CategoryIcon from '@mui/icons-material/Category';
import React from 'react';
import { ListItemContainer } from '../containers/plans/components/ListItemContainer';
import UnfoldLessIcon from '@mui/icons-material/UnfoldLess';
import UnfoldMoreIcon from '@mui/icons-material/UnfoldMore';
import DeleteIcon from '@mui/icons-material/Delete';

interface ProductListItemProps { 
	product: OrderProduct,
	open: boolean,
	onClick?: () => void;
	onExpandToggle?: () => void,
	onDeleteClick?: (productId: number) => void,
	showSku?: boolean;
}

export const ProductsListItem = ({ product, open, onExpandToggle, onDeleteClick, onClick, showSku}: ProductListItemProps) => {
	const theme = useTheme();
	return <ListItemContainer onClick={onClick}>
		<ListItemAvatar>
			<Avatar src={product?.imgUrl} variant='square' sx={{ width: 96, height: 96, borderBottomLeftRadius: 0, background: alpha(theme.palette.getContrastText(theme.palette.background.default), 0.12), color: 'divider' }}>
				<CategoryIcon fontSize='large'  />
			</Avatar>
		</ListItemAvatar>
		<ListItemText
			sx={{
				flex: 1,
				px: 2,
				'& > p': {color: 'primary.main'},
			}}
			primary={product.name}
			secondary={`€${product.pricePerUnit}`}
		/>
		{showSku && <ListItemText
			sx={{
				flex: 'unset',
				px: 2
			}}
			primary={product.sku}
		/>}
		<ListItemText
			sx={{
				flex: 'unset',
				px: 2,
				'& > span': {display:'inline-block', whiteSpace: 'nowrap', mr: 0.5},
				'& > p': {display:'inline-block'},
			}}
			primary={product.quantity}
			secondary={product.uom}
		/>
		{onDeleteClick && <Box p={2}>
			<IconButton onClick={() => onDeleteClick(product.id)}><DeleteIcon fontSize='small' /></IconButton>
		</Box>}
		{onExpandToggle && <Box p={2}>
			<IconButton
				aria-label="expand row"
				size="small"
				onClick={onExpandToggle}
			>
				{open ? <UnfoldLessIcon /> : <UnfoldMoreIcon />}
			</IconButton>
		</Box>}
	</ListItemContainer>;
};
