import React, { useEffect, useMemo, useRef, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { ImageListItem, ImageList, useMediaQuery, useTheme, Grid2, Typography, Tab, Tabs, Box, IconButton, ListItemText, List, ListItem, ListItemIcon, Avatar, Button } from '@mui/material';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import MoneyIcon from '@mui/icons-material/Money';
import SentimentVerySatisfiedIcon from '@mui/icons-material/SentimentVerySatisfied';
import { selectPlanAuthor, selectPlanDetails, selectPlanImages, selectPlanResourcesMaterials, selectPlanResourcesTools, selectPlanVendors, selectPlanVideos, selectPlanQuickOrders } from './planSlice';
import { Favorite as FavoriteIcon, FavoriteBorder as FavoriteBorderIcon } from '@mui/icons-material';
import PageWrapper from '../../components/PageWrapper';
import useAuth, { useAuthEffects } from '../../components/Auth';
import YouTubeEmbed from '../../components/YouTubeEmbed';
import { useAppSelector } from '../../hooks';
import { useAddFavouriteMutation, useRemoveFavouriteMutation, usePlanGetInfoPublicMutation } from '../../services/plans';
import { useOrderSubmitCustomOrderMutation, useOrderSubmitMutation } from '../../services/orders';
import { selectFavouriteIds } from './planSlice';
import { selectMyCompany } from '../accountSlice';
import { srcset } from '../../common/util';
import Linkify from 'react-linkify';
import { PlanViewResourcesTabContent } from './components/PlanViewResourcesTabContent';
import { ExpandableParagraph } from '../../components/ExpandableParagraph';

interface TabPanelProps {
	children?: React.ReactNode;
	index: number;
	value: number;
}

function CustomTabPanel(props: TabPanelProps) {
	const { children, value, index, ...other } = props;
	return (
		<div
			role="tabpanel"
			hidden={value !== index}
			id={`simple-tabpanel-${index}`}
			aria-labelledby={`simple-tab-${index}`}
			{...other}
		>
			{value === index && <Box sx={{ mt: 3 }}>{children}</Box>}
		</div>
	);
}

function a11yProps(index: number) {
	return {
		id: `simple-tab-${index}`,
		'aria-controls': `simple-tabpanel-${index}`,
	};
}

const PlanView = () => {
	useAuthEffects();

	const theme = useTheme();
	const [value, setValue] = React.useState(0);
	const materialsListRef = useRef<any>(null);
	const { id } = useParams();
	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));
	const {userProfile} = useAuth();

	const [planGetInfoPublicMutation] = usePlanGetInfoPublicMutation();
	const [orderSubmitMutation, { isSuccess, data }] = useOrderSubmitMutation();
	const [orderSubmitCustomOrderMutation] = useOrderSubmitCustomOrderMutation();
	const [addFavouriteMutation] = useAddFavouriteMutation();
	const [removeFavouriteMutation] = useRemoveFavouriteMutation();
	const navigate = useNavigate();
	const plan = useAppSelector(selectPlanDetails);
	const vendorsData = useAppSelector(selectPlanVendors);
	const productImages = useAppSelector(selectPlanImages);
	const instructionsVideos = useAppSelector(selectPlanVideos);
	const companyData = useAppSelector(selectPlanAuthor);
	const materialsData = useAppSelector(selectPlanResourcesMaterials);
	const toolsData = useAppSelector(selectPlanResourcesTools);
	const favouriteIds = useAppSelector(selectFavouriteIds);
	const myCompany = useAppSelector(selectMyCompany);
	const quickOrders = useAppSelector(selectPlanQuickOrders);

	const handleChange = (event: React.SyntheticEvent, newValue: number) => {
		setValue(newValue);
	};
	
	const [selectedResources, setSelectedResources] = useState(new Set<number>());
	const [vendorMessage, setVendorMessage] = useState('');
	const selectedItemsCount = useMemo(() => selectedResources.size, [selectedResources]);

	const [selectedVendorId, setSelectedVendorId] = useState<number | null>(null);
	const selectedVendor = useMemo(() => vendorsData.find((item) => item.id === selectedVendorId), [selectedVendorId]);

	useEffect(() => {
		if (id) {
			planGetInfoPublicMutation(id);
		}
	}, [id]);

	useEffect(() => {
		if (materialsData && selectedVendorId) {
			setSelectedResources(materialsData.reduce((acc, material) => acc.add(material.id), new Set<number>()));
		}
	}, [materialsData, selectedVendorId]);

	useEffect(() => {
		if (isSuccess) {
			navigate(`/purchases/${data.id}`);
		}
	}, [isSuccess, data]);

	const handleResourceCheckboxClick = (materialId: number) => {
		if (selectedResources.has(materialId)) {
			selectedResources.delete(materialId);
		} else {
			selectedResources.add(materialId);
		}
		setSelectedResources(new Set(selectedResources));
	};

	const handleVendorClick = (vendorId: number) => {
		if (vendorId !== selectedVendorId) {
			setSelectedVendorId(vendorId);
		}
		if (materialsListRef.current) {
			materialsListRef.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
		}
	};

	const handleSelectedVendorBtnClick = (e: any) => {
		e.stopPropagation(); setSelectedVendorId(null);
	};

	const handleCustomOrderSubmit = () => {
		if (userProfile && plan) {
			const resources = [...materialsData, ...toolsData].filter(({id}) => selectedResources.has(id));
			orderSubmitCustomOrderMutation({
				planId: plan.id,
				vendorId: selectedVendorId,
				resources: resources.map((resource) => ({ id: resource.id, quantity: resource.quantity })),
				message: vendorMessage,
			});
		}
	};

	const handleVendorMessageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
		setVendorMessage(e.target.value);
	};

	const handleAddFavouriteBtnClick = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>, planId: number) => {
		e.stopPropagation();
		if (!userProfile) {
			return;
		}
		addFavouriteMutation({ planId });
	};

	const handleRemoveFavouriteBtnClick = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>, planId: number) => {
		e.stopPropagation();
		removeFavouriteMutation(planId);
	};

	return plan && <PageWrapper>
		<Box display={isSmUp ? 'flex' : undefined}>
			<Box flex={isSmUp ? 1 : undefined}>
				<Typography variant='h2' sx={{flex:1}}>{plan.title}</Typography>
				<Typography variant='caption'>by @{companyData?.name}</Typography>
			</Box>
			{isSmUp && <Box>
				<IconButton 
					size='large'
					onClick={(e) => favouriteIds.has(plan.id) ? handleRemoveFavouriteBtnClick(e, plan.id) : handleAddFavouriteBtnClick(e, plan.id)}
				>
					{favouriteIds.has(plan.id) ? <FavoriteIcon color='primary' /> : <FavoriteBorderIcon color='disabled' />}
				</IconButton>
			</Box>}
		</Box>
		<Box sx={{ borderBottom: 1, borderColor: 'divider' }} ref={materialsListRef}>
			<Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
				<Tab label="Plan" {...a11yProps(0)} />
				{/* <Tab label="Instructions" {...a11yProps(1)} /> */}
				<Tab label="Supplies" {...a11yProps(1)} />
			</Tabs>
		</Box>
		<CustomTabPanel value={value} index={0}>
			<Grid2 container direction='row' spacing={2}>
				<Grid2 size={isSmUp ? 6 : 12}>
					<ExpandableParagraph text={plan.description} />
					{Boolean(plan.materialsCost || plan.timeCost || plan.ratingsSummary) && <List>
						{plan.materialsCost && <ListItem disablePadding>
							<ListItemIcon sx={{mr:2, minWidth: 0}}>
								<MoneyIcon />
							</ListItemIcon>
							<ListItemText primary={plan.materialsCost} />
						</ListItem>}
						{plan.timeCost && <ListItem disablePadding>
							<ListItemIcon sx={{mr:2, minWidth: 0}}>
								<AccessTimeIcon />
							</ListItemIcon>
							<ListItemText primary={plan.timeCost} />
						</ListItem>}
						{plan.ratingsSummary && <ListItem disablePadding>
							<ListItemIcon sx={{mr:2, minWidth: 0}}>
								<SentimentVerySatisfiedIcon />
							</ListItemIcon>
							<ListItemText primary={plan.ratingsSummary} />
						</ListItem>}
					</List>}
				</Grid2>
				<Grid2 size={isSmUp ? 6 : 12}>
					<ImageList
						variant="quilted"
						cols={4}
						rowHeight={100}
						sx={{m:0}}
					>
						{productImages.map((productImage, i) => {
							const colsRows = i === 0 ? 4 : 2;
							return (
								<ImageListItem key={productImage.imgUrl} cols={colsRows} rows={colsRows} sx={{borderRadius: 2, overflow: 'hidden', boxShadow: 3}}>
									<img
										{...srcset(productImage.imgUrl, 250, colsRows * 1, colsRows * 1)}
										alt={plan.title}
										loading="lazy"
										style={{overflow:'hidden'}}
									/>
								</ImageListItem>
							);})}
					</ImageList>
				</Grid2>
				<Grid2 size={12}>
					<Typography variant='h4'>How-to</Typography>
				</Grid2>
				{instructionsVideos.map((video) => (<Grid2 size={isSmUp ? 8 : 12} key={video.youtubeId}>
					<YouTubeEmbed videoId={video.youtubeId} title="Sample Video" />
				</Grid2>))}
				{plan.instructions && <Grid2>
					<Typography variant='body1' whiteSpace='pre-wrap'>{plan.instructions}</Typography>
				</Grid2>}
				<Grid2 size={12} sx={{mt:12}}>
					<Box display='flex' flexDirection='column' alignItems='center' sx={{mt: 4}}>
						<Avatar sx={{width:125, height: 125}}></Avatar>
						<Typography variant='h5' sx={{mt:2}}>@{companyData?.name}</Typography>
						{companyData?.bio && <Typography variant='body1' maxWidth={560} sx={{mt:2}}>
							{companyData.bio}
						</Typography>}
					</Box>
				</Grid2>
			</Grid2>
		</CustomTabPanel>
		<CustomTabPanel value={value} index={1}>
			<PlanViewResourcesTabContent 
				resources={materialsData}
				quickOrders={quickOrders}
				planId={plan.id}
			/>
		</CustomTabPanel>
	</PageWrapper>;
};

export default PlanView;

