import React, { useEffect } from 'react';
import PageWrapper from '../components/PageWrapper';
import { useAuthEffects } from '../components/Auth';
import { Button, Grid2 } from '@mui/material';
import { useAppSelector } from '../hooks';
import { selectMyCompany } from './accountSlice';
import { MyCompany, useUpdateMyCompanyMutation } from '../services/accounts';
import { SimpleFormTemplateProperty, SimpleFormPropertyType, SimpleFormField, useSimpleForm } from '../components/SimpleForm';

const template: SimpleFormTemplateProperty<MyCompany>[] = [{
// 	name: 'imgUrl',
// 	label: 'Profile image',
// 	type: SimpleFormPropertyType.Image,
// }, {
	name: 'name',
	label: 'Name',
	type: SimpleFormPropertyType.String,
}, {
	name: 'bio',
	label: 'Bio',
	type: SimpleFormPropertyType.Text,
}, {
	name: 'roleCustomer',
	label: 'I build based on inspiring content',
	type: SimpleFormPropertyType.Toggle,
}, {
	name: 'roleCreator',
	label: 'I show others how to build something',
	type: SimpleFormPropertyType.Toggle,
}, {
	name: 'roleVendor',
	label: 'I sell building materials',
	type: SimpleFormPropertyType.Toggle,
}];

const ProfileView = () => {
	useAuthEffects();
	const myCompany = useAppSelector(selectMyCompany);
	const [updateMyCompanyMutation] = useUpdateMyCompanyMutation();

	const {formData, resetFormData, handleFormFieldChange, hasFormChanged} = useSimpleForm<MyCompany>(template);

	useEffect(() => {
		if (myCompany) {
			resetFormData(myCompany);
		}
	}, [myCompany]);

	const handleFormSubmit = (e: React.FormEvent<HTMLFormElement>) => {
		e.preventDefault();
		updateMyCompanyMutation(formData);
	};

	return <PageWrapper>
		<form onSubmit={handleFormSubmit}>
			{myCompany && <Grid2 container spacing={1}>
				{template.map((property) => <Grid2 key={property.name} size={12}>
					<SimpleFormField property={property} value={formData[property.name]} onChange={handleFormFieldChange} />
				</Grid2>)}
				<Grid2 size={12}>
					<Button disabled={!hasFormChanged} variant='text' color='inherit' onClick={() => resetFormData(myCompany)}>Reset</Button>
					&nbsp;
					<Button type='submit' disabled={!hasFormChanged} variant='contained'>Save</Button>
				</Grid2>
			</Grid2>}
		</form>
	</PageWrapper>;
};

export default ProfileView;
