import { IconButton, Box, Checkbox, ListItemText, useTheme } from '@mui/material';
import React from 'react';
import { ResourceListItem } from '../../../types';
import CategoryIcon from '@mui/icons-material/Category';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import { ListItemContainer } from './ListItemContainer';

interface ResourceListItemProps { 
	resource: ResourceListItem,
	onClick?: () => void,
	onDeleteClick?: () => void,
	onEditClick?: () => void,
	open?: boolean;
	onExpandToggle?: () => void
	onCheckboxChange?: () => void;
	isChecked?: boolean;
	secondaryText?: string; 
}

export const ResourcesListItem = ({ resource, onClick, onExpandToggle, open, onCheckboxChange, isChecked, secondaryText, onDeleteClick, onEditClick }: ResourceListItemProps) => {
	const handleCheckboxChange = onCheckboxChange ? (e: React.ChangeEvent<HTMLInputElement>) => {
		e.stopPropagation();
		onCheckboxChange();
	} : undefined;
	const theme = useTheme();
	return <ListItemContainer onClick={onClick}>
		{/* <ListItemAvatar>
			<Avatar src={resource?.imgUrl} variant='square' sx={{ width: 96, height: 96, borderBottomLeftRadius: 0, background: alpha(theme.palette.getContrastText(theme.palette.background.default), 0.12), color: 'divider' }}>
				<CategoryIcon fontSize='large'  />
			</Avatar>
		</ListItemAvatar> */}
		<ListItemText
			sx={{
				flex: 1,
				px: 2,
				'& > p': {color: 'primary.main'},
			}}
			primary={resource.name}
			secondary={resource.description}
		/>
		<ListItemText
			sx={{
				flex: 'unset',
				'& > span': {display:'inline-block', whiteSpace: 'nowrap', mr: 0.5},
				'& > p': {display:'inline-block'},
			}}
			primary={resource.quantity}
			secondary={resource.uom}
		/>
		<Box pr={2}>
			{onCheckboxChange && <Checkbox
				onChange={handleCheckboxChange}
				checked={isChecked}
			/>}
		</Box>
		{Boolean(onEditClick || onDeleteClick || onExpandToggle) && <Box p={2}>
			{onEditClick && <IconButton onClick={onEditClick}>
				<EditIcon />
			</IconButton>}
			{onDeleteClick && <IconButton onClick={onDeleteClick}>
				<DeleteIcon />
			</IconButton>}
			{onExpandToggle && <IconButton onClick={() => onExpandToggle()}>
				{open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
			</IconButton>}
		</Box>}
	</ListItemContainer>;
};
